import React from "react";
import { weekDays } from "../utils/Constants.config";

const CalanderDays = () => {
  return (
    <thead>
      <tr>
        {weekDays.map((day) => (
          <th key={day}>
            <b>{day}</b>
          </th>
        ))}
      </tr>
    </thead>
  );
};

export default CalanderDays;
