import React from 'react'

const TotalleavesCard = () => {
  return (
      <div className="col-md-6 px-md-2 mb-3">
      <div className="cardBox h-100">
        <div className="cardHead d-flex justify-content-between align-items-center">
          <h3>Total Leaves</h3>
          <a href="#" className="viewall">
            View Detail{" "}
            <img
              src="images/next.svg"
              className="ps-1"
              alt=""
              width={20}
            />
          </a>
        </div>
        <div className="leaveContent d-flex align-items-center">
          <strong>
            10 <span>Leave Available</span>
          </strong>
          <strong>
            05 <span>Paid Leave</span>
          </strong>
          <strong>
            03 <span>Comp Off</span>
          </strong>
          <hr />
          <strong>
            10 <span>Sick Leave</span>
          </strong>
          <strong>
            05 <span>Leave Taken</span>
          </strong>
        </div>
      </div>
    </div>
  )
}

export default TotalleavesCard