import React,{useState,useEffect} from "react";

const TableHeader = ({tableHeader,setTableHeader,filterHeader}) => {
 

  return (
    <>
    <div className="d-flex align-items-center mb-4">
      <span className="fw-bold fs-6 text-black">08 Request</span>
      <ul className="nav nav-tabs leaveTabs ms-3">
        <li className="nav-item">
          <button
            className="nav-link active"
            data-bs-toggle="tab"
            data-bs-target="#pending"
            type="button"
          >
            Pending <span>4</span>
          </button>
        </li>
        <li className="nav-item">
          <button
            className="nav-link"
            data-bs-toggle="tab"
            data-bs-target="#approved"
            type="button"
          >
            Approved <span>4</span>
          </button>
        </li>
        <li className="nav-item">
          <button
            className="nav-link"
            data-bs-toggle="tab"
            data-bs-target="#rejected"
            type="button"
          >
            Rejected <span>0</span>
          </button>
        </li>
      </ul>
    
      
    </div>

<div style={{display:'flex',padding:"10px"}}>
{tableHeader.map((table,index)=>(
  <div className="d-flex p-1">
    {index!==0&&
    <>
    <input type="checkbox" className="form-check-input" name={table.name} onChange={(e)=>filterHeader(e,index)}  checked={table?.status?true:false}></input>
    <span>{table.name}</span></>
    }
    
    </div>
))}

</div>
</>
  );
};

export default TableHeader;
