import { createReducer } from 'redux-act';
import actions from '../actions/trainingCertificatesActions';

const initialState = {
    addTrainingCertificatePending: false,
    addTrainingCertificateRes: null,
    addTrainingCertificateError: null,

    getTrainingCertificatesPending: false,
    getTrainingCertificatesRes: null,
    getTrainingCertificatesError: null,

};
// add Qualification
const handleAddTrainingCertificateRequest = state => {
    return {
        ...state,
        ...{
            addTrainingCertificatePending: true,
            addTrainingCertificateRes: null,
            addTrainingCertificateError: null,
        },
    };
};
const handleAddTrainingCertificateSuccess = (state, payload) => {
    return {
        ...state,
        ...{
            addTrainingCertificatePending: false,
            addTrainingCertificateRes: payload,
            addTrainingCertificateError: null,
        },
    };
};
const handleAddTrainingCertificateError = (state, payload) => ({
    ...state,
    ...{
        addTrainingCertificatePending: false,
        addTrainingCertificateRes: null,
        addTrainingCertificateError: payload,
    },
});


// get Training Certificates
const handleGetTrainingCertificatesRequest = state1 => {
    return {
        ...state1,
        ...{
            getTrainingCertificatesPending: true,
            getTrainingCertificatesRes: null,
            getTrainingCertificatesError: null,
        },
    };
};
const handleGetTrainingCertificatesSuccess = (state1, payload) => {
    return {
        ...state1,
        ...{
            getTrainingCertificatesPending: false,
            getTrainingCertificatesRes: payload,
            getTrainingCertificatesError: null,
        },
    };
};
const handleGetTrainingCertificatesError = (state1, payload) => ({
    ...state1,
    ...{
        getTrainingCertificatesPending: false,
        getTrainingCertificatesRes: null,
        getTrainingCertificatesError: payload,
    },
});


// clear existing data
const clearTrainingCertificateStore = (state, payload) => {
    switch (payload) {
        case 'HANDLE_CLEAR_ADD_TRAININGCERTIFICATE_STORE':
            return {
                ...state,
                ...{
                    addTrainingCertificatePending: false,
                    addTrainingCertificateRes: null,
                    addTrainingCertificateError: null,
                },
            };
        
        default:
            return state;
    }
};



export default createReducer(
    {
        [actions.addTrainingCertificateRequest]: handleAddTrainingCertificateRequest,
        [actions.addTrainingCertificateSuccess]: handleAddTrainingCertificateSuccess,
        [actions.addTrainingCertificateError]: handleAddTrainingCertificateError,

        [actions.getTrainingCertificatesRequest]: handleGetTrainingCertificatesRequest,
        [actions.getTrainingCertificatesSuccess]: handleGetTrainingCertificatesSuccess,
        [actions.getTrainingCertificatesError]: handleGetTrainingCertificatesError,

        [actions.handleClearTrainingCertificateStore]: clearTrainingCertificateStore,
    },
    initialState,
);