import React from 'react'

const PointsStatement = () => {
  return (
      <div className="cardBox mb-3">
      <div className="cardHead d-flex align-items-center justify-content-between">
        <h3>Point Statement</h3>
        <div className="dropdown monthDropdown">
          <button
            type="button"
            className="dropdown-toggle"
            data-bs-toggle="dropdown"
          >
            Last 6 Month
          </button>
          <div className="dropdown-menu">
            <ul>
              <li>
                <a className="dropdown-item" href="#">
                  Today
                </a>
              </li>
              <li>
                <a className="dropdown-item" href="#">
                  Yesterday
                </a>
              </li>
              <li>
                <a className="dropdown-item" href="#">
                  This Month
                </a>
              </li>
              <li>
                <a className="dropdown-item" href="#">
                  Last Month
                </a>
              </li>
              <li>
                <a className="dropdown-item" href="#">
                  Last 3 Month
                </a>
              </li>
              <li>
                <a className="dropdown-item" href="#">
                  Last 6 Month
                </a>
              </li>
              <li>
                <a className="dropdown-item" href="#">
                  Last Year
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="table-responsive tableDiv">
        <table className="table">
          <thead>
            <tr>
              <th>Time Period</th>
              <th>Earned Points</th>
              <th>Redeemed Points</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Month Jan 2023</td>
              <td>200 Pts</td>
              <td>100 Pts</td>
            </tr>
            <tr>
              <td>Month Feb 2023</td>
              <td>200 Pts</td>
              <td>100 Pts</td>
            </tr>
            <tr>
              <td>Month Apr 2023</td>
              <td>100 Pts</td>
              <td>100 Pts</td>
            </tr>
            <tr>
              <td>Month May 2023</td>
              <td>100 Pts</td>
              <td>100 Pts</td>
            </tr>
            <tr>
              <td>Month Jun 2023</td>
              <td>50 Pts</td>
              <td>100 Pts</td>
            </tr>
            <tr>
              <td>Month Jul 2023</td>
              <td>100 Pts</td>
              <td>100 Pts</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  )
}

export default PointsStatement