import React from "react";

const CalanderEvent = ({ theam }) => {
  return (
    <div className={`shiftBox ${theam}`}>
      <div className="d-flex align-items-center gap-1">
        <span className="shiftNum">
          <img src="/images/present-icon.svg" alt="" width={23} />
        </span>
        <p>Full Day Present</p>
      </div>
    </div>
  );
};

export default CalanderEvent;
