import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
} from 'redux-saga/effects';
import { dropdownMasterActions as actions, notificationActions } from '../actions';
import { dropdownMasterService } from '../services';


// Get Department
function* handleGetFilteredDropdownDataRequest() {
    const action = yield take(actions.getFilteredDropdownDataRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
        res = yield call(dropdownMasterService.getFilteredDropdownData, dataParams);

        if (res.data.error) {
            yield put(actions.getFilteredDropdownDataError(res));

        } else {
            yield put(actions.getFilteredDropdownDataSuccess(res));
        }

    } catch (error) {
        yield put(actions.getFilteredDropdownDataError());
    }
}


// -----------Watchers-------------

function* handleGetFilteredDropdownDataWatcher() {
    yield takeEvery(
        actions.getFilteredDropdownDataRequest,
        handleGetFilteredDropdownDataRequest,
    );
}

function* rootDropdownMasterSaga() {
    yield all([
        handleGetFilteredDropdownDataRequest(),
        handleGetFilteredDropdownDataWatcher(),

    ]);
}

export default rootDropdownMasterSaga;
