import React from 'react'

const PaySlipRequest = () => {
  return (
      <div className="col-md-6 mb-3 px-md-2 mb-3">
              <div className="cardBox position-relative h-100">
                <h6 className="fw-semibold mb-4">Payslip Request</h6>
                <p className="text-secondary mb-4">
                  You can request payslip of previous month (January) from here
                </p>
                <div className="pt-3">
                  <a
                    href="#"
                    className=" btn-org-gradient text-white"
                  >
                    Request Now
                  </a>
                </div>
                <div className="lottiemain">
                  <lottie-player
                    src="images/files.json"
                    background="transparent"
                    speed={1}
                    style={{ width: "60px", height: "100%" }}
                    loop
                    autoPlay
                  ></lottie-player>
                </div>
              </div>
            </div>
  )
}

export default PaySlipRequest