import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
  } from 'redux-saga/effects';
  import { loginActions as actions,notificationActions } from '../actions';
  import { loginService } from '../services';


  
  function* handleUserLoginRequest() {
    const action = yield take(actions.userLoginRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(loginService.userLogin, dataParams);
      if (res.isError) {
        yield put(actions.userLoginError(res));
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage || 'Successfully logged in', alertType: 'error' }));
        // yield put(notificationActions.notificationError({ message: res?.errorMessage, alertType: "error" }));
      } else {
        yield put(actions.userLoginSuccess(res));
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage || 'Successfully logged in', alertType: 'success' }));
        // yield put(notificationActions.notificationSuccess({message: res?.data?.message, alertType:"success"}))
      }
    } catch (error) {
      yield put(actions.userLoginError());
      // yield put(notificationActions.notificationError({ message: res?.errorMessage, alertType: "error" }));
    }
  }
  
  function* handleGetUserLoginWatcher() {
    yield takeEvery(
      actions.userLoginRequest,
      handleUserLoginRequest,
    );
  }
  
  function* rootLoginSaga() {
    yield all([
        handleUserLoginRequest(),
        handleGetUserLoginWatcher(),
    ]);
  }
  
  export default rootLoginSaga;
  