import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
} from 'redux-saga/effects';
import { trainingCertificatesActions as actions, notificationActions } from '../actions';
import { trainingCertificateService } from '../services';

// add Department
function* handleAddTrainingCertificateRequest() {
    const action = yield take(actions.addTrainingCertificateRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
        res = yield call(trainingCertificateService.addTrainingCertificate, dataParams);
        if (res.isError) {
            yield put(actions.addTrainingCertificateError(res));
            yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
        } else {
            yield put(actions.addTrainingCertificateSuccess(res));
            yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
        }
    } catch (error) {
        yield put(actions.addTrainingCertificateError());
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
}

// Get Department
function* handleGetTrainingCertificatesRequest() {
    const action = yield take(actions.getTrainingCertificatesRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
        res = yield call(trainingCertificateService.getTrainingCertificates, dataParams);

        if (res.data.error) {
            yield put(actions.getTrainingCertificatesError(res));

        } else {
            yield put(actions.getTrainingCertificatesSuccess(res));
        }

    } catch (error) {
        yield put(actions.getTrainingCertificatesError());
    }
}


// -----------Watchers-------------
function* handleAddTrainingCertificateWatcher() {
    yield takeEvery(
        actions.addTrainingCertificateRequest,
        handleAddTrainingCertificateRequest,
    );
}

function* handleGetTrainingCertificatesWatcher() {
    yield takeEvery(
        actions.getTrainingCertificatesRequest,
        handleGetTrainingCertificatesRequest,
    );
}

function* rootTrainingCertificatesSaga() {
    yield all([
        handleAddTrainingCertificateRequest(),
        handleAddTrainingCertificateWatcher(),

        handleGetTrainingCertificatesRequest(),
        handleGetTrainingCertificatesWatcher(),

    ]);
}

export default rootTrainingCertificatesSaga;
