import React, { useEffect, useState } from "react";

import CalanderHeader from "./CalanderHeader";
import CalanderBody from "./CalanderBody";
import { months } from "../utils/Constants.config";

const Calander = () => {
  const [dateMatrix, setDateMatrix] = useState([[]]);

  const [currentMonth, setCurrentMonth] = useState(new Date().getMonth());
  const [currentYear, setCurrentYear] = useState(new Date().getFullYear());
  function previousBtnFun() {
    decreseMonths();
  }
  function nextBtnFun() {
    increaseMonth();
  }
  function increaseMonth() {
    if (currentMonth >= 11) {
      setCurrentMonth(0);
      setCurrentYear(currentYear + 1);
    } else {
      setCurrentMonth(currentMonth + 1);
    }
  }
  function decreseMonths() {
    if (currentMonth === 0) {
      setCurrentMonth(11);
      setCurrentYear(currentYear - 1);
    } else {
      setCurrentMonth(currentMonth - 1);
    }
  }

  function generateDateMatrix() {
    let dateMatrix = [[]];
    let week = 0;
    let firstDate = new Date(currentYear, currentMonth, 1);
    let endDate = new Date(currentYear, currentMonth + 1, 0);
    for (let i = 1; i <= firstDate.getDay(); i++) {
      dateMatrix[week].push(null);
    }
    for (let i = 1; i <= endDate.getDate(); i++) {
      if (dateMatrix[week].length === 7) {
        week++;
        dateMatrix[week] = [];
      }
      dateMatrix[week].push(i);
    }
    
    setDateMatrix(dateMatrix);
  }
  useEffect(() => {
    generateDateMatrix();
  }, [currentMonth]);

  return (
    <>
      <div className="cardBox mb-3 customCalander">
        <CalanderHeader
          months={months}
          currentMonth={currentMonth}
          currentYear={currentYear}
          previousBtnFun={previousBtnFun}
          nextBtnFun={nextBtnFun}
        />
        <CalanderBody dateMatrix={dateMatrix} />
      </div>
    </>
  );
};

export default Calander;
