import React from 'react'
import Button from '../../commanComponent/Button'

const CreateKudosRequest = () => {
  return (
      <form className="requestForm">
      <div className="form-group mb-3">
        <strong>Kudos Type</strong>
        <a href className="cstmSelect">
          Select Kudos Type
        </a>
      </div>
      <div className="form-group mb-3">
        <strong>Select User</strong>
        <a
          href="#selectTeam"
          data-bs-toggle="modal"
          className="cstmSelect"
        >
          Select User
        </a>
      </div>
      <div className="form-group mb-3">
        <strong>Kudos Point</strong>
        <a
          href="#selectKudosPoint"
          data-bs-toggle="modal"
          className="cstmSelect"
        >
          Give Points
        </a>
      </div>
      <div className="form-group mb-3">
        <strong>Kudos Description</strong>
        <textarea
          className="form-control"
          rows={4}
          placeholder="Enter here...."
          defaultValue={""}
        />
      </div>
      <div className="form-group">
       
        <Button btnClassName={"btn btn-primary w-100"}>
        Create Request
        </Button>
      </div>
    </form>
  )
}

export default CreateKudosRequest