import React from "react";
import Button from "../components/commanComponent/Button";
import Image from "../components/commanComponent/Image";
import {ChatTypeSelection, GroupChat, GroupChatContent, IndidualChatContent, IndividualChat} from "../components/chat";

const Chat = () => {
  return (
    <div className="row">
      <div className="col-md-4">
        <div className="cardBox chatLeft">
          <div className="d-flex justify-content-between align-items-center border-bottom mb-3 pb-3">
            <h2 className="fs-5 text-primary mb-0">Chats</h2>
          
            <Button btnClassName={"addUserBtn"}  data-bs-target="#addMemberModal"
              data-bs-toggle="modal">
                <Image imgSrc={"images/add-user.svg"} altText={""} width={20}/>
              </Button>
          </div>
        <ChatTypeSelection/>
          <div className="tab-content">
           <IndividualChat/>
           <GroupChat/>
           </div>
        </div>
      </div>
      <div className="col-md-8 ps-md-0">
        <div className="cardBox chatRight p-0">
          <IndidualChatContent/>
          <GroupChatContent/>
          
        </div>
      </div>
    </div>
  );
};

export default Chat;
