import React from 'react'
import Input from '../../commanComponent/Input'
import CommentPost from './CommentPost'

const CommentSection = () => {
  return (
      <div className="cardBox mb-3">
      <div className="comment">
        <div className="d-flex align-items-center profilePost mb-3">
          <figure className="m-0">
            <img src="images/amber.png" alt="" />
          </figure>
          <div className="input-group ">
            
            <Input type={"text"} inputClassName={"form-control"} placeholder={"Write a comment"}/>
          </div>
        </div>
      <CommentPost/>
        <a href="#" className="loadComment">
          {" "}
          Load more Comments
        </a>
      </div>
    </div>
  )
}

export default CommentSection