import { createAction } from 'redux-act';
// add Department action
const addTrainingCertificateRequest = createAction('ADD_TRAININGCERTIFICATE_REQUEST', data => data);
const addTrainingCertificateSuccess = createAction('ADD_TRAININGCERTIFICATE_SUCCESS', data => data);
const addTrainingCertificateError = createAction('ADD_TRAININGCERTIFICATE_ERROR');

// get Department action
const getTrainingCertificatesRequest = createAction('GET_TRAININGCERTIFICATE_REQUEST', data => data);
const getTrainingCertificatesSuccess = createAction('GET_TRAININGCERTIFICATE_SUCCESS', data => data);
const getTrainingCertificatesError = createAction('GET_TRAININGCERTIFICATE_ERROR');

// clear existing Department action
const handleClearTrainingCertificateStore = createAction('HANDLE_CLEAR_TRAININGCERTIFICATE_STORE', data => data);
// const toastMsgAction = createAction('SUCCESS', data=> data);

export default {
    addTrainingCertificateRequest,
    addTrainingCertificateSuccess,
    addTrainingCertificateError,

    getTrainingCertificatesRequest,
    getTrainingCertificatesSuccess,
    getTrainingCertificatesError,

    handleClearTrainingCertificateStore,
    // toastMsgAction,

}