import { createReducer } from 'redux-act';
import actions from '../actions/mrfActions';

const initialState = {
  createUpdateMrfPending: false,
  createUpdateMrfRes: null,
  createUpdateMrfError: null,

  getInterviewerListPending: false,
  getInterviewerListRes: null,
  getInterviewerListError: null,

  getMrfDataByIdPending: false,
  getMrfDataByIdRes: null,
  getMrfDataByIdError: null,

  submitDraftMrfPending: false,
  submitDraftMrfRes: null,
  submitDraftMrfError: null,

  getalAUserMrfPending: false,
  getalAUserMrfRes: null,
  getalAUserMrfError: null,

  getMrfDashboardAndHierarchyPending: false,
  getMrfDashboardAndHierarchyRes: null,
  getMrfDashboardAndHierarchyError: null,

  getMrfForApprovalDataPending: false,
  getMrfForApprovalDataRes:null,
  getMrfForApprovalDataError:null,

  approveRejectMrfPending: false,
  approveRejectMrfRes:null,
  approveRejectMrfError:null,

};
// Create Update Mrf
const handleCreateUpdateMrfRequest = state => {
  return {
    ...state,
    ...{
      createUpdateMrfPending: true,
      createUpdateMrfRes: null,
      createUpdateMrfError: null,
    },
  };
};
const handleCreateUpdateMrfSuccess = (state, payload) => {
  return {
    ...state,
    ...{
      createUpdateMrfPending: false,
      createUpdateMrfRes: payload,
      createUpdateMrfError: null,
    },
  };
};
const handleCreateUpdateMrfError = (state, payload) => ({
  ...state,
  ...{
    createUpdateMrfPending: false,
    createUpdateMrfRes: null,
    createUpdateMrfError: payload,
  },
});


