// clear existing data
const handleClearMrfStore = (state, payload) => {
  switch (payload) {
    case 'HANDLE_CLEAR_CREATE_UPDATE_MRF_STORE':
      return {
        ...state,
        ...{
          createUpdateMrfPending: false,
          createUpdateMrfRes: null,
          createUpdateMrfError: null,
        },
      };
    case 'HANDLE_CLEAR_GET_MRF_DATA_BY_ID_STORE':
      return {
        ...state,
        ...{
          getMrfDataByIdPending: false,
          getMrfDataByIdRes: null,
          getMrfDataByIdError: null,
        },
      };

    case 'HANDLE_CLEAR_SUBMIT_DRAFT_MRF_STORE':
      return {
        ...state,
        ...{
          submitDraftMrfPending: false,
          submitDraftMrfRes: null,
          submitDraftMrfError: null,
        },
      };

    case 'HANDLE_CLEAR_GET_ALL_USER_MRF_STORE':
      return {
        ...state,
        ...{
          getAllUserMrfPending: false,
          getAllUserMrfRes: null,
          getAllUserMrfError: null,
        },
      };
    case 'HANDLE_CLEAR_APPROVE_REJECT_MRF_STORE':
      return {
        ...state,
        ...{
          approveRejectMrfPending: false,
          approveRejectMrfRes: null,
          approveRejectMrfError: null,
        },
      };



    default:
      return state;
  }
};



export default createReducer(
  {
    [actions.createUpdateMrfRequest]: handleCreateUpdateMrfRequest,
    [actions.createUpdateMrfSuccess]: handleCreateUpdateMrfSuccess,
    [actions.createUpdateMrfError]: handleCreateUpdateMrfError,

    [actions.getInterviewerListRequest]: handleGetInterviewerListRequest,
    [actions.getInterviewerListSuccess]: handleGetInterviewerListSuccess,
    [actions.getInterviewerListError]: handleGetInterviewerListError,

    [actions.getMrfDataByIdRequest]: handleGetMrfDataByIdRequest,
    [actions.getMrfDataByIdSuccess]: handleGetMrfDataByIdSuccess,
    [actions.getMrfDataByIdError]: handleGetMrfDataByIdError,


    [actions.getMrfDataByIdRequest]: handleGetMrfDataByIdRequest,
    [actions.getMrfDataByIdSuccess]: handleGetMrfDataByIdSuccess,
    [actions.getMrfDataByIdError]: handleGetMrfDataByIdError,

    [actions.submitDraftMrfRequest]: handleSubmitDraftMrfRequest,
    [actions.submitDraftMrfSuccess]: handleSubmitDraftMrfSuccess,
    [actions.submitDraftMrfError]: handleSubmitDraftMrfError,

    [actions.getAllUserMrfRequest]: handleGetAllUserMrfRequest,
    [actions.getAllUserMrfSuccess]: handleGetAllUserMrfSuccess,
    [actions.getAllUserMrfError]: handleGetAllUserMrfError,

    [actions.getMrfDashboardAndHierarchyRequest]: handleGetMrfDashboardAndHierarchyRequest,
    [actions.getMrfDashboardAndHierarchySuccess]: handleGetMrfDashboardAndHierarchySuccess,
    [actions.getMrfDashboardAndHierarchyError]: handleGetMrfDashboardAndHierarchyError,

    [actions.getMrfForApprovalDataRequest]: handleGetMrfForApprovalDataRequest,
    [actions.getMrfForApprovalDataSuccess]: handleGetMrfForApprovalDataSuccess,
    [actions.getMrfForApprovalDataError]: handleGetMrfForApprovalDataError,

    [actions.approveRejectMrfRequest]: handleApproveRejectMrfRequest,
    [actions.approveRejectMrfSuccess]: handleApproveRejectMrfSuccess,
    [actions.approveRejectMrfError]: handleApproveRejectMrfError,


    [actions.handleClearMrfStore]: handleClearMrfStore,
  },
  initialState,
);