import React from 'react'

const Input = ({ type, name, placeholder, onChange, className, id, value, isDisabled}) => {
  return (
    <input
      type={type ||"text"}
      name={name}
      id={id}
      placeholder={placeholder}
      className={`form-control ${className}`}
      onChange={onChange}
      value={value}
      disabled={isDisabled ||false}
    ></input>
  )

}

export default Input