import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
  } from 'redux-saga/effects';
  import { emailTemplateAction as actions,notificationActions } from '../actions';
  import { EmailTemplateService } from '../services';

  // add Department
  function* handleAddEmailTypeRequest() {
    const action = yield take(actions.addEmailTypeRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(EmailTemplateService.addEmailType, dataParams);
      if (res.isError) {
        yield put(actions.addEmailTypeError(res));     
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
      } else {
        yield put(actions.addEmailTypeSuccess(res));  
        yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
      }
    } catch (error) {
      yield put(actions.addEmailTypeError());
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
  }
//get email type
  function* handleGetEmailTypeRequest() {
    const action = yield take(actions.getEmailTypeRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(EmailTemplateService.getEmailType,dataParams);

      if (res.data.error) {
        yield put(actions.getEmailTypeError(res));
        
      } else {
        yield put(actions.getEmailTypeSuccess(res));
        console.log(res?.data?.message,"res?.data?.message")
      }

    } catch (error) {
      yield put(actions.getDepartmentError());
    }
  }
