  // -----------Watchers-------------
  function* handleAddemailTypeWatcher() {
    yield takeEvery(
      actions.addEmailTypeRequest,
      handleAddEmailTypeRequest,
    );
  }

  function* handleGetEmailTypeWatcher() {
    yield takeEvery(
      actions.getEmailTypeRequest,
      handleGetEmailTypeRequest,
    );
  }

  function* handleaddEmailTemplateWatcher() {
    yield takeEvery(
      actions.addEmailTemplateRequest,
      handleAddEmailTemplateRequest,
    );
  }

  function* handleGetEmailTemplateWatcher() {
    yield takeEvery(
      actions.getEmailTemplateRequest,
      handleGetEmailTemplateRequest,
    );
  }

  function* handleUpdateEmailTemplateWatcher() {
    yield takeEvery(
      actions.updateEmailTemplateRequest,
      handleUpdateDepartmentRequest,
    );
  }

  function* handleDeleteEmailTemplateWatcher() {
    yield takeEvery(
      actions.deleteEmailTemplateRequest,
      handleDeleteTemplateRequest,
    );
  }

  function* handleremoveImageWatcher() {
    yield takeEvery(
      actions.RemoveImageRequest,
      handleRemoveImageRequest,
    );
  }

  function* handleAddActionWatcher() {
    yield takeEvery(
      actions.AddActionTemplateRequest,
      handleAddActionRequest,
    );
  }

  function* handleGetActionWatcher() {
    yield takeEvery(
      actions.getActionTemplateRequest,
      handleGetActionRequest,
    );
  }

  function* handleUpdateActionWatcher() {
    yield takeEvery(
      actions.UpdataActionTemplateRequest,
      handleUpdateActionRequest,
    );
  }

  function* handleDeleteActionWatcher() {
    yield takeEvery(
      actions.DeleteActionTemplateRequest,
      handleDeleteActionRequest,
    );
  }

  function* handleGetActionTypeWatcher() {
    yield takeEvery(
      actions.GetActionTypeTemplateRequest,
      handleGetActionTypeRequest,
    );
  }

 
  
  function* rootEmailTemplate() {
    yield all([
        handleAddEmailTypeRequest(),
        handleAddemailTypeWatcher(),

        handleGetEmailTypeRequest(),
        handleGetEmailTypeWatcher(),

        handleAddEmailTemplateRequest(),
        handleaddEmailTemplateWatcher(),

        handleGetEmailTemplateRequest(),
        handleGetEmailTemplateWatcher(),

        handleUpdateDepartmentRequest(),
        handleUpdateEmailTemplateWatcher(),

        handleDeleteTemplateRequest(),
        handleDeleteEmailTemplateWatcher(),

        handleremoveImageWatcher(),
        handleRemoveImageRequest(),

        handleAddActionWatcher(),
        handleAddActionRequest(),

        handleGetActionRequest(),
        handleGetActionWatcher(),

        handleUpdateActionRequest(),
        handleUpdateActionWatcher(),

        handleDeleteActionRequest(),
        handleDeleteActionWatcher(),

        handleGetActionTypeRequest(),
        handleGetActionTypeWatcher(),
    ]);
  }
  
  export default rootEmailTemplate;
  