import React from 'react'

const LikeCommentShare = () => {
  return (
      <div className="likeDetail">
        <div className="dropdown">
          <a href="#">
            <img src="images/like.svg" alt="" width={16} /> Like
          </a>
          <div className="emojiMenu">
            <ul className="d-flex align-items-center">
              <li>
                <a href="#">
                  <img
                    src="images/likered.svg"
                    className="me-0"
                    style={{
                      width: "18px",
                      height: "18px",
                      minWidth: "18px",
                    }}
                  />
                </a>
              </li>
              <li>
                <a href="#">
                  <lottie-player
                    src="images/angry-emoji.json"
                    style={{ width: "30px", height: "30px" }}
                    loop
                    autoPlay
                  />
                </a>
              </li>
              <li>
                <a href="#">
                  <lottie-player
                    src="images/celebration-emoji.json"
                    style={{ width: "30px", height: "30px" }}
                    loop
                    autoPlay
                  />
                </a>
              </li>
              <li>
                <a href="#">
                  <lottie-player
                    src="images/puppy-eyes-emoji.json"
                    style={{ width: "30px", height: "30px" }}
                    loop
                    autoPlay
                  />
                </a>
              </li>
              <li>
                <a href="#">
                  <lottie-player
                    src="images/emoji-love.json"
                    style={{ width: "30px", height: "30px" }}
                    loop
                    autoPlay
                  />
                </a>
              </li>
            </ul>
          </div>
        </div>
        <a href="" style={{}}>
          <img src="images/comment.svg" alt="" width={16} /> Comment
        </a>
        <a href="#shareMessage" data-bs-toggle="modal">
          <img src="images/share.svg" alt="" width={16} />
          Share
        </a>
      </div>
  )
}

export default LikeCommentShare