import React from 'react';
import App from './App'
import {
  Routes,
  Route,  
} from "react-router-dom";
import PrivateRoute from "./components/PrivateRoute.js";
import PageNotFound from "./components/PageNotFound";
import MainDashboard from "./view/MainDashboard.js";
import Chat from "./view/Chat.js";
import Community from "./view/Community.js";
import Kudos from "./view/Kudos.js";
import LeaveManagement from "./view/LeaveManagement.js";
import HolidayList from "./view/HolidayList.js";
import OtherAttendance from "./view/OtherAttendance.js";
import UserShiftDetails from "./view/UserShiftDetails.js";




const ShiftManagement = React.lazy(() => import("./view/ShiftManagement.js"));
const ShiftSetting = React.lazy(()=> import('./view/ShiftSetting.js'))

const Login = React.lazy(() => import("./components/auth/login/Login"));
const Dashboard = React.lazy(() => import("../src/view/dashboard.js"));
const AttendanceDashboard = React.lazy(
  () => import("./view/AttendanceDashboard.js")
);
const EmailTemplate = React.lazy(()=> import('./view/EmailTemplate.js'))
const PayrollDashboard = React.lazy(() => import("./view/PayrollDashboard.js"));
const TicketManagementSystem = React.lazy(() => import("./view/TicketManagementSystem.js"));
const Department =React.lazy(()=>import("./view/Department.js"));
const Designation =React.lazy(()=>import("./view/Designation.js"));
const Employee =React.lazy(()=>import("./view/Employee.js"))
const Mrf = React.lazy(() => import("./view/Mrf.js"));
// const NewMrfProcess = React.lazy(() => import("./components/hiring/mrf/NewMrfProcess.js"));
const MrfProcessSteps =React.lazy(()=>import('./components/hiring/mrf/MrfProcessSteps.js'));
const InterviewPanel = React.lazy(() => import("./view/InterviewPanel.js"));
const EmployeeRefereal = React.lazy(() => import("./view/EmployeeRefereal.js"));
const HrDeshboard = React.lazy(() => import("./view/hrDeshboard.js"));
const Profile = React.lazy(() => import("./view/Profile.js"));



const routesList = () => (
  <Routes>
    <Route path="/" element={<App />}>
            <Route path="/" element={<PrivateRoute Component={Dashboard} />}>
            <Route
                path="/my-profile"
                element={<PrivateRoute Component={Profile} />}
              />
              <Route
                index
                element={<PrivateRoute Component={MainDashboard} />}
              />
              <Route path="chat" element={<PrivateRoute Component={Chat} />} />
              <Route
                path="community"
                element={<PrivateRoute Component={Community} />}
              />
              <Route
                path="kudos"
                element={<PrivateRoute Component={Kudos} />}
              />
              <Route
                path="attendance-dashboard"
                element={<PrivateRoute Component={AttendanceDashboard} />}
              />
              <Route
                path="leave-management"
                element={<PrivateRoute Component={LeaveManagement} />}
              />
              <Route
                path="holiday-list"
                element={<PrivateRoute Component={HolidayList} />}
              />
              <Route
                path="other-attendance"
                element={<PrivateRoute Component={OtherAttendance} />}
              />
              <Route
                path="shift-management"
                element={<PrivateRoute Component={ShiftManagement} />}
              />
               <Route
                path="shift-setting"
                element={<PrivateRoute Component={ShiftSetting} />}
              />
               <Route
                path="shift-management/:userId/:startDate/:endDate"
                element={<PrivateRoute Component={UserShiftDetails} />}
              />
               <Route
                path="payroll"
                element={<PrivateRoute Component={PayrollDashboard} />}
              />
               <Route
                path="ticket-management-system"
                element={<PrivateRoute Component={TicketManagementSystem} />}
              />
               <Route
                path="department"
                element={<PrivateRoute Component={Department} />}
              />
              <Route
                path="designation"
                element={<PrivateRoute Component={Designation} />}
              />
              <Route
                path="employee"
                element={<PrivateRoute Component={Employee} />}
              />
              <Route
                path="mrf"
                element={<PrivateRoute Component={Mrf} />}
              />
              <Route
                path="/interview-panel"
                element={<PrivateRoute Component={InterviewPanel} />}
              />
               <Route
                path="/employee-refereal"
                element={<PrivateRoute Component={EmployeeRefereal} />}
              />
              <Route
                path="/mrf-process-step/:mrfType/:id?"
                element={<PrivateRoute Component={MrfProcessSteps} />}
              />
               <Route
                path="/hr-deshboard"
                element={<PrivateRoute Component={HrDeshboard} />}
              />
              <Route 
              path='/email-template'
              element={<PrivateRoute Component={EmailTemplate} />}
              />
            </Route>
            <Route path="/login" element={<Login />}></Route>
            {/* <Route
              path="/test"
              element={<PrivateRoute Component={Test} />}
            ></Route> */}
            <Route path="*" element={<PageNotFound />}></Route>
    </Route>
          </Routes>
)
export default routesList;