import { createReducer } from 'redux-act';
import actions from '../actions/locationActions';

const initialState = {
  getAllLocationsPending: false,
  getAllLocationsRes: null,
  getAllLocationsError: null,

};
// Create Update Mrf
const handleGetAllLocationsRequest = state => {
  return {
    ...state,
    ...{
      getAllLocationsPending: true,
      getAllLocationsRes: null,
      getAllLocationsError: null,
    },
  };
};
const handleGetAllLocationsSuccess = (state, payload) => {
  return {
    ...state,
    ...{
      getAllLocationsPending: false,
      getAllLocationsRes: payload,
      getAllLocationsError: null,
    },
  };
};
const handleGetAllLocationsError = (state, payload) => ({
  ...state,
  ...{
    getAllLocationsPending: false,
    getAllLocationsRes: null,
    getAllLocationsError: payload,
  },
});



export default createReducer(
  {
    [actions.getAllLocationsRequest]: handleGetAllLocationsRequest,
    [actions.getAllLocationsSuccess]: handleGetAllLocationsSuccess,
    [actions.getAllLocationsError]: handleGetAllLocationsError,

  },
  initialState,
);