import { fork } from 'redux-saga/effects';
import { 
  rootCommonSagas,
  rootLoginSaga,
  rootDepartmentSaga,
  rootDesignationSaga,
  rootSidebarSaga,
  rootQualificationSaga,
  rootTrainingCertificatesSaga,
  rootDropdownMasterSaga,
  rootPositionSaga,
  rootMrfSaga,
  rootLocationSaga,
  rootEmployeeSaga,
  rootMrfInterviewSaga,
  rootShiftSaga,
  rootEmailTemplate
} from './sagas';



function* rootSaga() {
  yield fork(rootCommonSagas);
  yield fork(rootLoginSaga);
  yield fork(rootDepartmentSaga);
  yield fork(rootDesignationSaga);
  yield fork(rootEmailTemplate);
  yield fork(rootSidebarSaga);
  yield fork(rootShiftSaga);
  yield fork(rootQualificationSaga);
  yield fork(rootTrainingCertificatesSaga);
  yield fork(rootDropdownMasterSaga)
  yield fork(rootPositionSaga);
  yield fork(rootMrfSaga);
  yield fork(rootLocationSaga);
  yield fork(rootEmployeeSaga);
  yield fork(rootMrfInterviewSaga);
}

export default rootSaga;