import {
  take,
  put,
  call,
  all,
  takeLatest,
  takeEvery,
} from 'redux-saga/effects';
import { employeeActions as actions, notificationActions } from '../actions';
import { employeeService } from '../services';



// Get Department
function* handleGetAllEmployeeRequest() {
  const action = yield take(actions.getAllEmployeeRequest.getType());
  const dataParams = action.payload;
  let res;
  try {
    res = yield call(employeeService.getAllEmployee, dataParams);

    if (res.data.error) {
      yield put(actions.getAllEmployeeError(res));

    } else {
      yield put(actions.getAllEmployeeSuccess(res));
    }

  } catch (error) {
    yield put(actions.getAllEmployeeError());
  }
}

// Get Department
function* handleGetEmployeeForReportingManagerRequest() {
  const action = yield take(actions.getEmployeeForReportingManagerRequest.getType());
  let res;
  try {
    res = yield call(employeeService.getEmployeeForReportingManager);

    if (res.data.error) {
      yield put(actions.getEmployeeForReportingManagerError(res));

    } else {
      yield put(actions.getEmployeeForReportingManagerSuccess(res));
    }

  } catch (error) {
    yield put(actions.getEmployeeForReportingManagerError());
  }
}

// -----------Watchers-------------

function* handleGetAllEmployeeWatcher() {
  yield takeEvery(
    actions.getAllEmployeeRequest,
    handleGetAllEmployeeRequest,
  );
}

function* handleGetEmployeeForReportingManagerWatcher() {
  yield takeEvery(
    actions.getEmployeeForReportingManagerRequest,
    handleGetEmployeeForReportingManagerRequest,
  );
}

function* rootEmployeeSaga() {
  yield all([

    handleGetAllEmployeeRequest(),
    handleGetAllEmployeeWatcher(),

    handleGetEmployeeForReportingManagerRequest(),
    handleGetEmployeeForReportingManagerWatcher(),

  ]);
}
export default rootEmployeeSaga;

