import React from 'react'
import { useState,useEffect } from 'react'
const CustomePagination = ({record,itemPerPage}) => {
      const[pageNo,setPageNo]=useState([])
      const[currentPage,setCurrentPage]=useState(1)
      const handelPageNo=(type)=>{
            if(type==="pageDown"){
                  if(currentPage===1){
                        alert("this is last page")
                  }else{
                        setCurrentPage(currentPage-1)
                  }
            }if (type==="pageUp") {
                  if(currentPage===Math.ceil(record/itemPerPage)){
                        alert("this is last page")
                  }else{
                        setCurrentPage(currentPage+1)
                  }
                  
            } else {
                  
            }

      }
useEffect(()=>{
      let newArray=[]
      for(let i=1;i<=(Math.ceil(record/itemPerPage));i++){
            newArray.push(i)
      }
      setPageNo(newArray)
},[record])

  const handleChange = async (page) => {
    setCurrentPage(page+1);
  }   
   
  return (
      <div className="customPagination mt-4">
      <ul className="pagination">
        <li className="page-item previous-item">
          <button className="page-link" aria-label="Previous" onClick={()=>handelPageNo("pageDown")}>
            <span aria-hidden="true">
              <img src="images/arrowprw.svg" width={6} />
            </span>
          </button>
        </li>
        {pageNo.map((page ,ind)=>(
              <li className={`${page===currentPage?"page-item active":"page-item"}`}
              >
              <span className="page-link" onClick={() => handleChange(ind)}>
                {page}
              </span>
            </li>

        ))}
      
        {/* <li className="page-item">
          <a className="page-link" href="#">
            2
          </a>
        </li>
        <li className="page-item">
          <a className="page-link" href="#">
            3
          </a>
        </li> */}
        <li className="page-item">
          <a className="page-link" href="#">
            <img src="images/threedot.svg" alt="" width={16} />
          </a>
        </li>
        <li className="page-item next-item">
          <button className="page-link" onClick={()=>handelPageNo("pageUp")}>
            <span aria-hidden="true">
              <img src="images/arrownxt.svg" width={6} />
            </span>
          </button>
        </li>
      </ul>
    </div>
  )
}

export default CustomePagination