import React from "react";

const CalanderHeader = ({
  months,
  currentMonth,
  currentYear,
  nextBtnFun,
  previousBtnFun,
}) => {
  return (
    <div className="text-center pb-3 d-flex align-items-center justify-content-between">
      <h3 className="mb-0 fs-5 fw-semibold">
        {`${months[currentMonth]} ${currentYear}`}
      </h3>
      <div className="d-flex gap-2">
        <button type="button" className="calenderbtn" onClick={previousBtnFun}>
          <img
            src="./images/prev-icon.svg"
            alt=""
            className="pe-2"
            width={14}
          />
          Previous
        </button>
        <button type="button" className="calenderbtn" onClick={nextBtnFun}>
          Next
          <img
            src="./images/next-icon.svg"
            alt=""
            className="ps-2"
            width={14}
          />
        </button>
      </div>
    </div>
  );
};

export default CalanderHeader;
