import axios from 'axios';
import React, { useState } from 'react';
import { setDataOnLocal, getDataOnLocal } from '../../../utils/AppHelper';
import { getAccessTokenData, logoutUser } from '../../../utils/AxoisHelper';
import { axiosConfig } from "../../../utils/Constants.config";


let headers = {
  Authorization: "",
  token_type: "",
  account_id: "",
};

const getRequest = () => ({ method: 'GET' })
const getRequestWithParams = (data) => ({ method: 'GET', params: data })
const postRequest = (data) => ({ method: 'POST', data })
const postRequestWithParams = (data) => ({ method: 'POST', params: data })

const checkTokenIsValid = async (res) => {
  const dataMsg = res?.data?.message?.toLowerCase() || '';
  const dataStatus = res?.data?.status || '';
  const errorMsgArr = ['invalid token', 'token is missing', 'token is expired', 'token is expired.', 'token not found in db.', 'invalid token.']
  //console.log('--checkTokenIsValid--', {dataStatus, dataMsg});
  if (dataStatus !== 200 && errorMsgArr.indexOf(dataMsg?.toLowerCase()) !== -1) {
    await logoutUser(); 
  }
}
const callApi = async (url, options, hearderParams, additionalParams = null) => {
  let res = null

  if (additionalParams?.isInterceptors) {
    // Add a request interceptor
    const axoisRequestInterceptors = axios.interceptors.request.use(function (config) {
      // console.log('--interceptors-- request', config);
      return config;
    }, function (error) {
      console.log('--interceptors-- error', error);
      return Promise.reject(error);
    });

    // Add a response interceptor
    const axoisResponseInterceptors = axios.interceptors.response.use(function (response) {
      // console.log('--interceptors-- response', response);
      checkTokenIsValid(response);
      return response;
    }, function (error) {
      // console.log('--interceptors-- error', error);
      return Promise.reject(error);
    });
    //axios.interceptors.request.eject(axoisRequestInterceptors);
  }

  try {
    const { account_id, authorization, password } = hearderParams
    console.log('ADDITIONAL PARAMS',url,additionalParams)
    headers = {
      'Content-Type': 'application/json',
      'account_id': '',
      'token_type': '',
      'Authorization': '',
    };
    if (authorization) {
      headers['Authorization'] = authorization
    }
    if (password && additionalParams?.isPassword) {
      headers['password'] = password
    }
    if (account_id) {
      headers['account_id'] = account_id
    }

    res = await axios({ url, ...options, headers })
    res = await res.data
    return res
  } catch (error) {
    res = { isError: true, msg: error.message }
    return res
  }
}

const getAxiosConfig = (dataParam) => {
  let {withCredentials}=axiosConfig
  let dataParams =dataParam || {}
  const accessTokenData = getAccessTokenData();

  if (dataParams['Cache-Control'] !== undefined && dataParams['Cache-Control'] !== null) {
    headers['Pragma'] = dataParams['Cache-Control'];
  } else {
    delete headers['Cache-Control']
  }
  if (dataParams['Accept'] !== undefined) {
    headers['Content-Type'] = dataParams['Content-Type']
  } else {
    headers['Content-Type'] = 'application/json'
  }
  
  // headers.account_id = accessTokenData?.account_id || '';
  headers['authtoken'] = accessTokenData || '';
  const { type } = dataParams;
  const CancelToken = axios.CancelToken;
  const source = CancelToken.source();
  let config = { baseURL: '', cancelToken: source.token, headers, withCredentials };
  if (type === 'upload') {
    config.onUploadProgress = (event) => {

  };
  } else if (type === 'download') {
    config.onDownloadProgress= (event) => {

      };
  }

  const axiosInstance = axios.create(config);
  axiosInstance.interceptors.response.use(function (response) {
    // console.log('--interceptors axiosInstance--', response);
    checkTokenIsValid(response);
    return response;
  });
  return { axiosInstance, source, config }
};

const getFomatedError = (err) => {
  const { code, errorMessage,message, name } = err;
  let error = { isError: true, errorCode: code, errorMessage: errorMessage || message, errorName: name };
  return error;
}

export {
  headers,
  getRequest,
  getRequestWithParams,
  postRequest,
  postRequestWithParams,
  callApi,
  getAxiosConfig,
  getFomatedError,
};