import CryptoJS from "crypto-js";

const encryptValue = (text, secret = '123456') => {
    const secretHex = stringToHash(secret);
    const ciphertext = CryptoJS.AES.encrypt(text, secretHex).toString();
    return ciphertext;
  };

  const decryptValue = (ciphertext, secret = '123456') => {
    const secretHex = stringToHash(secret);  
    const bytes = CryptoJS.AES.decrypt(ciphertext, secretHex); 
    const originalText = bytes.toString(CryptoJS.enc.Utf8);  
    return originalText;
  };

const stringToHash = secret => {
  return CryptoJS.SHA256(secret).toString();    
  };

  const getBadgeColor = status => {
    switch (status) {
      case 'Active':
        return 'greenBadge';
      case 'Inactive':
        return 'redBadge';
      case 'pending':
        return 'blueBadge';
      case 'reject':
        return 'redBadge';
        case 'approve':
        return 'greenBadge';
      default:
        return 'blue';
    }
  };

  const convertSelectObject =(label,value,data)=>{
    let selectData =[]
    if(typeof(key) !== 'object'){
      data?.map(data => {
        if(data?.is_active === true){
          selectData.push({ label: data?.[label], value: data?.[value] });
        }
    });
    return selectData
    }
  }

  const toTitleCase = (data)=>{
    return data
    ?.split('_')
    ?.map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
    ?.join(' ');
  }

  const transformKey = (key) => {
    const withSpaces = key.replace(/_/g, ' ');
    return withSpaces
      .split(' ')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  };

export {
    encryptValue,
    decryptValue,
    getBadgeColor,
    convertSelectObject,
    toTitleCase,
    transformKey


}