import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";
//add Depatment
const addEmailType = (params) => {
  const { axiosInstance,config } = getAxiosConfig();
  const response = axiosInstance({
    url: `${baseUrls.main}/${apiUrls.admin.createEmailType}`,
    method: "POST",
    data: params,
    withCredentials:config?.withCredentials,
    headers: config.headers ||null,
    mode: "no-cors",
  })
    .then((res) => {
      return res;
    })
    .catch((error) => {
      console.log(error.response.data,"errror.............")
      return getFomatedError(error?.response?.data);
    });
  return response;
};

const getEmailType = (params)=>{
    const { axiosInstance ,config,source} = getAxiosConfig();
    const response = axiosInstance({
      url: `${baseUrls.main}/${apiUrls.admin.getEmailType}`,
      method: "GET",
      // params: params,
      withCredentials:config?.withCredentials,
      headers: config.headers ||null,
      mode: "no-cors",
    })
      .then((res) => {
        return res;
      })
      .catch((error) => {
        return getFomatedError(error);
      });
    return response;

}

