import React from 'react'

const UpCommingHoliday = () => {
  return (
      <div className="col-md-6 mb-3 px-md-2 mb-3">
      <div className="cardBox h-100">
        <div className="cardHead d-flex justify-content-between align-items-center">
          <h3>Upcoming Holidays</h3>
          <a href="#" className="viewall">
            View All{" "}
            <img
              src="images/next.svg"
              className="ps-1"
              alt=""
              width={20}
            />
          </a>
        </div>
        <ul className="announcement upcoming">
          <li>
            Maha Shivratri
            <span>08 Mar 2024</span>
          </li>
          <li>
            Holi
            <span>25 Mar 2024</span>
          </li>
          <li>
            Eid-Ul-Fitr
            <span>11 Apr 2024</span>
          </li>
          <li className="mb-0">
            Independence Day
            <span>15 Aug 2024</span>
          </li>
        </ul>
      </div>
    </div>
  )
}

export default UpCommingHoliday