import { createReducer } from 'redux-act';
import actions from '../actions/employeeActions';

const initialState = {

  getEmployeeForReportingManagerPending: false,
  getEmployeeForReportingManagerRes: null,
  getEmployeeForReportingManagerError: null,

  getAllEmployeePending: false,
  getAllEmployeeRes: null,
  getAllEmployeeError: null,

};

//get Employee For Reporting Manager
const handleGetEmployeeForReportingManagerRequest = state1 => {
  return {
    ...state1,
    ...{
      getEmployeeForReportingManagerPending: true,
      getEmployeeForReportingManagerRes: null,
      getEmployeeForReportingManagerError: null,
    },
  };
};
const handleGetEmployeeForReportingManagerSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getEmployeeForReportingManagerPending: false,
      getEmployeeForReportingManagerRes: payload,
      getEmployeeForReportingManagerError: null,
    },
  };
};
const handleGetEmployeeForReportingManagerError = (state1, payload) => ({
  ...state1,
  ...{
    getEmployeeForReportingManagerPending: false,
    getEmployeeForReportingManagerRes: null,
    getEmployeeForReportingManagerError: payload,
  },
});


// get department 
const handleGetAllEmployeeRequest = state1 => {
  return {
    ...state1,
    ...{
      getAllEmployeePending: true,
      getAllEmployeeRes: null,
      getAllEmployeeError: null,
    },
  };
};
const handleGetAllEmployeeSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getAllEmployeePending: false,
      getAllEmployeeRes: payload,
      getAllEmployeeError: null,
    },
  };
};
const handleGetAllEmployeeError = (state1, payload) => ({
  ...state1,
  ...{
    getAllEmployeePending: false,
    getAllEmployeeRes: null,
    getAllEmployeeError: payload,
  },
});

// clear existing data
const clearEmployeeStore = (state, payload) => {
  switch (payload) {
    case 'HANDLE_CLEAR_GET_EMPLOYEE_FOR_REPORTING_MANAGER_STORE':
      return {
        ...state,
        ...{
          getEmployeeForReportingManagerPending: false,
          getEmployeeForReportingManagerRes: null,
          getEmployeeForReportingManagerError: null,
        },
      };
    default:
      return state;
  }
};

export default createReducer(
  {

    [actions.getEmployeeForReportingManagerRequest]: handleGetEmployeeForReportingManagerRequest,
    [actions.getEmployeeForReportingManagerSuccess]: handleGetEmployeeForReportingManagerSuccess,
    [actions.getEmployeeForReportingManagerError]: handleGetEmployeeForReportingManagerError,

    [actions.getAllEmployeeRequest]: handleGetAllEmployeeRequest,
    [actions.getAllEmployeeSuccess]: handleGetAllEmployeeSuccess,
    [actions.getAllEmployeeError]: handleGetAllEmployeeError,

  },
  initialState,
);
