import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";


  // get Employee For Reporting Manager
const getAssignedInterview = (params) => {
  const { axiosInstance ,config} = getAxiosConfig();
  const response = axiosInstance({
    url: `${baseUrls.main}/${apiUrls.mrfInterview.getAssignedInterview}`,
    method: "GET",
    params: params,
    headers: config.headers ||null,
    mode: "no-cors",
  })
    .then((res) => {
      return res;
    })
    .catch((error) => {
      return getFomatedError(error);
    });
  return response;
};

  // Get Question By Interview Stage
  const getQuestionByInterviewStage = (params) => {
    const { axiosInstance ,config} = getAxiosConfig();
    const response = axiosInstance({
      url: `${baseUrls.main}/${apiUrls.mrfInterview.getQuestionByInterviewStage}`,
      method: "GET",
      params: params,
      headers: config.headers ||null,
      mode: "no-cors",
    })
      .then((res) => {
        return res;
      })
      .catch((error) => {
        return getFomatedError(error);
      });
    return response;
  };
  
    // Get Question By Interview Stage
    const fillFeedbackForm = (params) => {
      const { axiosInstance ,config} = getAxiosConfig();
      const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.mrfInterview.fillFeedbackForm}`,
        method: "POST",
        data: params,
        headers: config.headers ||null,
        mode: "no-cors",
      })
        .then((res) => {
          return res;
        })
        .catch((error) => {
          return getFomatedError(error?.response?.data);
        });
      return response;
    };


     // Get Question By Interview Stage
     const getInterviewDetailsById = (params) => {
      const { axiosInstance ,config} = getAxiosConfig();
      const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.mrfInterview.getInterviewDetailsById}`,
        method: "GET",
        params: params,
        headers: config.headers ||null,
        mode: "no-cors",
      })
        .then((res) => {
          return res;
        })
        .catch((error) => {
          return getFomatedError(error);
        });
      return response;
    };
    
export default {
 
    getAssignedInterview,
    getQuestionByInterviewStage,
    fillFeedbackForm,
    getInterviewDetailsById
    
}