// add shift
const handleAddShiftRequest = state => {
  return {
    ...state,
    ...{
      addShiftPending: true,
      addShiftRes: null,
      addShiftError: null,
    },
  };
};
const handleAddShiftSuccess = (state, payload) => {
  return {
    ...state,
    ...{
      addShiftPending: false,
      addShiftRes: payload,
      addShiftError: null,
    },
  };
};
const handleAddShiftError = (state, payload) => ({
  ...state,
  ...{
    addShiftPending: false,
    addShiftRes: null,
    addShiftError: payload,
  },
});


