// clear existing data
const clearShiftStore = (state, payload) => {
  switch (payload) {
    case 'HANDLE_CLEAR_ADD_SHIFT_STORE':
      return {
        ...state,
        ...{
          addShiftPending: false,
          addShiftRes: null,
          addShiftError: null,
        },
      };
    case 'HANDLE_CLEAR_UPDATE_SHIFT_STORE':
      return {
        ...state,
        ...{
          updateShiftPending: false,
          updateShiftRes: null,
          updateShiftError: null,
        },
      };
      case 'HANDLE_CLEAR_DELETE_SHIFT_STORE':
      return {
        ...state,
        ...{
          deleteShiftPending: false,
          deleteShiftRes: null,
          deleteShiftError: null,
        },
      };
      case 'HANDLE_CLEAR_ADD_SHIFT_CONFIGURATION_STORE':
        return {
          ...state,
          ...{
            addShiftConfigurationPending: false,
            addShiftConfigurationRes: null,
            addShiftConfigurationError: null,
          },
        };
        case 'HANDLE_CLEAR_ADD_SHIFT_CALENDAR_STORE':
          return {
            ...state,
            ...{
              addShiftCalendarPending: false,
              addShiftCalendarRes: null,
              addShiftCalendarError: null,
            },
          };
        case 'HANDLE_CLEAR_UPDATE_SHIFT_CALENDAR_STORE':
          return {
            ...state,
            ...{
              updateShiftCalendarPending: false,
              updateShiftCalendarRes: null,
              updateShiftCalendarError: null,
            },
          };
          case 'HANDLE_CLEAR_DELETE_SHIFT_CALENDAR_STORE':
          return {
            ...state,
            ...{
              deleteShiftCalendarPending: false,
              deleteShiftCalendarRes: null,
              deleteShiftCalendarError: null,
            },
          };
    default:
      return state;
  }
};



export default createReducer(
  {
    [actions.addShiftRequest]: handleAddShiftRequest,
    [actions.addShiftSuccess]: handleAddShiftSuccess,
    [actions.addShiftError]: handleAddShiftError,

    [actions.getAllShiftRequest]: handleGetAllShiftRequest,
    [actions.getAllShiftSuccess]: handleGetAllShiftSuccess,
    [actions.getAllShiftError]: handleGetAllShiftError,

    [actions.updateShiftRequest]: handleUpdateShiftRequest,
    [actions.updateShiftSuccess]: handleUpdateShiftSuccess,
    [actions.updateShiftError]: handleUpdateShiftError,

    [actions.deleteShiftRequest]: handleDeleteShiftRequest,
    [actions.deleteShiftSuccess]: handleDeleteShiftSuccess,
    [actions.deleteShiftError]: handleDeleteShiftError,

    [actions.addShiftConfigurationRequest]: handleAddShiftConfigurationRequest,
    [actions.addShiftConfigurationSuccess]: handleAddShiftConfigurationSuccess,
    [actions.addShiftConfigurationError]: handleAddShiftConfigurationError,

    [actions.getShiftConfigurationRequest]: handleGetShiftConfigurationRequest,
    [actions.getShiftConfigurationSuccess]: handleGetShiftConfigurationSuccess,
    [actions.getShiftConfigurationError]: handleGetShiftConfigurationError,

     [actions.addShiftCalendarRequest]: handleAddShiftCalendarRequest,
    [actions.addShiftCalendarSuccess]: handleAddShiftCalendarSuccess,
    [actions.addShiftCalendarError]: handleAddShiftCalendarError,

    [actions.getAllShiftCalendarRequest]: handleGetAllShiftCalendarRequest,
    [actions.getAllShiftCalendarSuccess]: handleGetAllShiftCalendarSuccess,
    [actions.getAllShiftCalendarError]: handleGetAllShiftCalendarError,

    [actions.updateShiftCalendarRequest]: handleUpdateShiftCalendarRequest,
    [actions.updateShiftCalendarSuccess]: handleUpdateShiftCalendarSuccess,
    [actions.updateShiftCalendarError]: handleUpdateShiftCalendarError,

    [actions.deleteShiftCalendarRequest]: handleDeleteShiftCalendarRequest,
    [actions.deleteShiftCalendarSuccess]: handleDeleteShiftCalendarSuccess,
    [actions.deleteShiftCalendarError]: handleDeleteShiftCalendarError,

    [actions.handleClearShiftStore]: clearShiftStore,
  },
  initialState,
);