import { createAction } from 'redux-act';

const userLoginRequest = createAction('USER_LOGIN_REQUEST', data => data);
const userLoginSuccess = createAction('USER_LOGIN_SUCCESS', data => data);
const userLoginError = createAction('USER_LOGIN_ERROR');

// clear existing Designation action
const handleClearloginStore = createAction('HANDLE_CLEAR_LOGIN_STORE',data => data);

export default {
    userLoginRequest,
    userLoginSuccess,
    userLoginError,

    handleClearloginStore,
}