import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";


  // get Employee For Reporting Manager
const getEmployeeForReportingManager = (params) => {
  const { axiosInstance ,config,source} = getAxiosConfig();
  const response = axiosInstance({
    url: `${baseUrls.main}/${apiUrls.employee.getEmployeeForReportingManager}`,
    method: "GET",
    headers: config.headers ||null,
    mode: "no-cors",
  })
    .then((res) => {
      return res;
    })
    .catch((error) => {
      return getFomatedError(error);
    });
  return response;
};
// get Department
const getAllEmployee= (params) => {
    const { axiosInstance ,config} = getAxiosConfig();
    const response = axiosInstance({
      url: `${baseUrls.main}/${apiUrls.employee.getAllEmployee}`,
      method: "POST",
      data: params,
      headers: config.headers ||null,
      mode: "no-cors",
    })
      .then((res) => {
        return res;
      })
      .catch((error) => {
        return getFomatedError(error);
      });
    return response;
  };
export default {
    getEmployeeForReportingManager,
    getAllEmployee,
}