import React from 'react'
import Image from '../../commanComponent/Image'
import Button from '../../commanComponent/Button'

const CreatePost = () => {
  return (
      <div className="cardBox mb-3 p-0">
      <div className="createpost">
        <div className="p-3">
          <div className="cardHead">
            <h3>Create post</h3>
          </div>
          <div className="d-flex align-items-center profilePost">
            <figure className="m-0">
              {/* <img src="images/amber.png" alt="" /> */}
              <Image imgSrc={'images/amber.png'} altText={''}/>
            </figure>
            <span>Who are you appreciating today? Use '+' '@' '#'</span>
          </div>
        </div>
        <hr className="w-100 my-0" />
        <div className="d-flex justify-content-between align-items-center p-3">
          <div className="d-flex gap-3">
            <a href="#createPost" data-bs-toggle="modal">
              {/* <img src="images/gallery-icon.svg" alt="" width={28} /> */}
              <Image imgSrc={'images/gallery-icon.svg'} width={28} altText={""}/>
            </a>
            {/* <a href="#createPost" data-bs-toggle="modal">
              <img src="images/vedio-icon.svg" alt="" width={28} />
            </a> */}
           <a href="#createPost" data-bs-toggle="modal">
                  <Image imgSrc={'images/vedio-icon.svg'} width={28}/>
                  </a>
          </div>
          {/* <a href="#" className="btn-org-gradient px-3 py-2">
            POST
          </a> */}
          <Button btnClassName={"btn-org-gradient px-3 py-2"}>Post</Button>
        </div>
      </div>
    </div>
  )
}

export default CreatePost