import React, { Suspense } from "react";
import { Outlet } from "react-router-dom";
import { CustomToast } from './components/globalComponents';

function App() {
  return (
    <div className="App">
      {/* <ToastContainer onClose={() => dispatch(removeAlert())} /> */}
      <Suspense fallback={<div>Loading...</div>}>
        {/* <BrowserRouter>
         
        </BrowserRouter> */}
        <CustomToast />
        <Outlet/>
      </Suspense>
    </div>
  );
}

export default App;
