import React from 'react'

const LeaveStaticsCard = () => {
  return (
      <div className="col-md-7 px-md-2 mb-3">
      <div className="cardBox h-100">
        <div className="cardHead">
          <h3>Leaves Statistics</h3>
          <p>Employee's six month leave Statistics</p>
        </div>
        <div id="leaveStatistics" style={{ height: "250px" }} />
      </div>
    </div>
  )
}

export default LeaveStaticsCard