import { createReducer } from 'redux-act';
import actions from '../actions/mrfInterviewAction';

const initialState = {

  getAssignedInterviewPending: false,
  getAssignedInterviewRes: null,
  getAssignedInterviewError: null,

  getQuestionByInterviewStagePending: false,
  getQuestionByInterviewStageRes: null,
  getQuestionByInterviewStageError: null,

  fillFeedbackFormPending: false,
  fillFeedbackFormRes: null,
  fillFeedbackFormError: null,

  getInterviewDetailsByIdPending: false,
  getInterviewDetailsByIdRes: null,
  getInterviewDetailsByIdError: null,

};

//get Employee For Reporting Manager
const handleGetAssignedInterviewRequest = state1 => {
  return {
    ...state1,
    ...{
      getAssignedInterviewPending: true,
      getAssignedInterviewRes: null,
      getAssignedInterviewError: null,
    },
  };
};
const handleGetAssignedInterviewSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getAssignedInterviewPending: false,
      getAssignedInterviewRes: payload,
      getAssignedInterviewError: null,
    },
  };
};
const handleGetAssignedInterviewError = (state1, payload) => ({
  ...state1,
  ...{
    getAssignedInterviewPending: false,
    getAssignedInterviewRes: null,
    getAssignedInterviewError: payload,
  },
});

//get Question By Interview Stage
const handleGetQuestionByInterviewStageRequest = state1 => {
  return {
    ...state1,
    ...{
      getQuestionByInterviewStagePending: true,
      getQuestionByInterviewStageRes: null,
      getQuestionByInterviewStageError: null,
    },
  };
};
const handleGetQuestionByInterviewStageSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getQuestionByInterviewStagePending: false,
      getQuestionByInterviewStageRes: payload,
      getQuestionByInterviewStageError: null,
    },
  };
};
const handleGetQuestionByInterviewStageError = (state1, payload) => ({
  ...state1,
  ...{
    getQuestionByInterviewStagePending: false,
    getQuestionByInterviewStageRes: null,
    getQuestionByInterviewStageError: payload,
  },
});


//fill Feedback Form
const handleFillFeedbackFormRequest = state1 => {
  return {
    ...state1,
    ...{
      fillFeedbackFormPending: true,
      fillFeedbackFormRes: null,
      fillFeedbackFormError: null,
    },
  };
};
const handleFillFeedbackFormSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      fillFeedbackFormPending: false,
      fillFeedbackFormRes: payload,
      fillFeedbackFormError: null,
    },
  };
};
const handleFillFeedbackFormError = (state1, payload) => ({
  ...state1,
  ...{
    fillFeedbackFormPending: false,
    fillFeedbackFormRes: null,
    fillFeedbackFormError: payload,
  },
});

//=get Interview Details By Id
const handleGetInterviewDetailsByIdRequest = state1 => {
  return {
    ...state1,
    ...{
      getInterviewDetailsByIdPending: true,
      getInterviewDetailsByIdRes: null,
      getInterviewDetailsByIdError: null,
    },
  };
};
const handleGetInterviewDetailsByIdSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getInterviewDetailsByIdPending: false,
      getInterviewDetailsByIdRes: payload,
      getInterviewDetailsByIdError: null,
    },
  };
};
const handleGetInterviewDetailsByIdError = (state1, payload) => ({
  ...state1,
  ...{
    getInterviewDetailsByIdPending: false,
    getInterviewDetailsByIdRes: null,
    getInterviewDetailsByIdError: payload,
  },
});

// clear existing data
const handelClearMrfInterviewStore = (state, payload) => {
  switch (payload) {
    case 'HANDLE_CLEAR_GET_ASSIGNED_INTERVIEW_STORE':
      return {
        ...state,
        ...{
            getAssignedInterviewPending: false,
            getAssignedInterviewRes: null,
            getAssignedInterviewError: null,
        },
      };
      case 'HANDLE_CLEAR_GET_QUESTION_BY_INTERVIEW_STAGE_STORE':
        return {
          ...state,
          ...{
              getQuestionByInterviewStagePending: false,
              getQuestionByInterviewStageRes: null,
              getQuestionByInterviewStageError: null,
          },
        };
        case 'HANDLE_CLEAR_FILL_FEEDBACK_FORM_STORE':
        return {
          ...state,
          ...{
            fillFeedbackFormPending: false,
            fillFeedbackFormRes: null,
            fillFeedbackFormError: null,
          },
        };
    default:
      return state;
  }
};



export default createReducer(
  {


    [actions.getAssignedInterviewRequest]: handleGetAssignedInterviewRequest,
    [actions.getAssignedInterviewSuccess]: handleGetAssignedInterviewSuccess,
    [actions.getAssignedInterviewError]: handleGetAssignedInterviewError,

    [actions.getQuestionByInterviewStageRequest]: handleGetQuestionByInterviewStageRequest,
    [actions.getQuestionByInterviewStageSuccess]: handleGetQuestionByInterviewStageSuccess,
    [actions.getQuestionByInterviewStageError]: handleGetQuestionByInterviewStageError,

    
    [actions.fillFeedbackFormRequest]: handleFillFeedbackFormRequest,
    [actions.fillFeedbackFormSuccess]: handleFillFeedbackFormSuccess,
    [actions.fillFeedbackFormError]: handleFillFeedbackFormError,

    [actions.getInterviewDetailsByIdRequest]: handleGetInterviewDetailsByIdRequest,
    [actions.getInterviewDetailsByIdSuccess]: handleGetInterviewDetailsByIdSuccess,
    [actions.getInterviewDetailsByIdError]: handleGetInterviewDetailsByIdError,

    [actions.handelClearMrfInterviewStore]:handelClearMrfInterviewStore,

  },
  initialState,
);