import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
  } from 'redux-saga/effects';
  import { locationActions as actions,notificationActions } from '../actions';
  import { locationService } from '../services';

  // get locations 
  function* handleGetAllLocationsRequest() {
    const action = yield take(actions.getAllLocationsRequest.getType());
    let res;
    try {
        res = yield call(locationService.getAllLocations);
      if (res.isError) {
        yield put(actions.getAllLocationsError(res));     
      } else {
        yield put(actions.getAllLocationsSuccess(res));  
      }
    } catch (error) {
      yield put(actions.getAllLocationsError());
    }
  }

  // -----------Watchers-------------
  function* handleGetAllLocationsWatcher() {
    yield takeEvery(
      actions.getAllLocationsRequest,
      handleGetAllLocationsRequest,
    );
  }
  
  function* rootLocationSaga() {
    yield all([
        handleGetAllLocationsRequest(),
        handleGetAllLocationsWatcher(),

      
    ]);
  }
  
  export default rootLocationSaga;
  