import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";

// get TrainingCertificate
const getFilteredDropdownData = (params) => {
    const { axiosInstance, config } = getAxiosConfig();
    const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.dropdownMaster.getFilteredDropdownData}`,
        method: "GET",
        params: params,
        headers: config.headers || null,
        mode: "no-cors",
    })
        .then((res) => {
            return res;
        })
        .catch((error) => {
            return getFomatedError(error);
        });
    return response;
};

export default {
    getFilteredDropdownData,
}