import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";

//add TrainingCertificate
const addTrainingCertificate = (params) => {
    const { axiosInstance, config } = getAxiosConfig();
    const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.admin.addTrainingCertificate}`,
        method: "POST",
        data: params,
        headers: config.headers || null,
        mode: "no-cors",
    })
        .then((res) => {
            return res;
        })
        .catch((error) => {
            return getFomatedError(error?.response?.data);
        });
    return response;
};

// get TrainingCertificate
const getTrainingCertificates = (params) => {
    const { axiosInstance, config } = getAxiosConfig();
    const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.admin.getTrainingCertificates}`,
        method: "GET",
        params: params,
        headers: config.headers || null,
        mode: "no-cors",
    })
        .then((res) => {
            return res;
        })
        .catch((error) => {
            return getFomatedError(error);
        });
    return response;
};



export default {
    addTrainingCertificate,
    getTrainingCertificates,
}