import React from 'react'
import LikeCommentShare from './LikeCommentShare'

const Post = () => {
  return (
      <div className="cardBox mb-3 p-0">
      <div className="createpost">
        <div className="p-3 pb-0">
          <div className="d-flex align-items-center profilePost mb-2">
            <figure className="m-0">
              <img src="images/amber.png" alt="" />
            </figure>
            <strong>
              Nikita Prajapat<span>8 Aug - 25m</span>
            </strong>
          </div>
          <p>
            Lorem Ipsum is simply dummy text of the printing and
            typesetting industry. Lorem Ipsum has been the industry's
            standard dummy text ever since the 1500s,
          </p>
          <div
            className="anniversaryone"
            style={{ backgroundImage: "url(images/anniverssry-bg.png)" }}
          >
            <span>Congratulation on your 1st year Anniversary</span>
          </div>
        </div>
      </div>
      <div className="likesMain d-flex px-3">
        <div className="d-flex justify-content-between align-items-center w-100">
          <div className="likes">
            <a href="#alllikes" data-bs-toggle="modal">
              <img
                src="images/likefill-Icon.png"
                alt=""
                width={20}
                className="position-relative"
              />
            </a>
            <a href="#alllikes" data-bs-toggle="modal">
              <img
                src="images/heartfill-Icon.png"
                alt=""
                width={20}
                className="heartIcon"
              />
            </a>
            <span>206</span>
          </div>
        </div>
        <div className="commentShare">
          <span>10 comments</span>
          <span>6 shares</span>
        </div>
      </div>
      <hr className="my-0" />
   <LikeCommentShare/>
    </div>
  )
}

export default Post