import React from "react";
import { TopEarners, YourPoints } from "../components/community/feeds";
import {CreateKudosRequest, KudosRequest, PointsStatement} from "../components/community/kudos";
import Button from "../components/commanComponent/Button";

const Kudos = () => {
  return (
    <div className="row">
      <div className="col-md-8 pe-2">
        <div className="row">
           <div className="col-md-6 pe-md-2 mb-3">
          <YourPoints/>
          </div>
          <div className="col-md-6 ps-md-2 mb-3">
            <TopEarners/>
          </div>
        
        </div>
        <PointsStatement/>
       <KudosRequest/>
      </div>
      <div className="col-md-4 ps-2">
        <div className="requestBox">
         
          <Button btnClassName={"btn btn-primary gradientBg pendingBtn w-100 mb-3"} dataBsTarget={"#pendingRequestModal"} dataBsToggle={"modal"}> Your Pending Requests</Button>
          <div className="cardBox">
            <div className="cardHead">
              <h3 className="d-flex align-items-center gap-2">
                <lottie-player
                  src="images/star-icon.json"
                  background="transparent"
                  speed={1}
                  style={{ width: "22px", height: "22px" }}
                  loop
                  autoPlay
                />{" "}
                Create Kudos Request
              </h3>
            </div>
           <CreateKudosRequest/>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Kudos;
