import React from 'react';
import Button from 'react-bootstrap/Button';
import CustomSpinner from './CustomSpinner';

export default function CustomButton(props) {
  const { 
    variant,
    classNames,
    isLoading,
    text,
    handleClick,
    styles,
    size,
    isDisabled,
    icon,
  } = props
 
  return (
    <Button
      className={classNames || 'btn-org-gradient text-white'}
      variant={variant || ''}
      disabled={isDisabled || isLoading}
      onClick={!isLoading ? handleClick : null}
      style={styles || { display: "flex", justifyContent: "center", gap: "5px" }}
    >
       {icon} {text}
      <CustomSpinner isShow={isLoading}  size={size ||'sm'} />
    </Button>
  )
}