import { createReducer } from 'redux-act';
import actions from '../actions/departmentAction';

const initialState = {
  addDepartmentPending: false,
  addDepartmentRes: null,
  addDepartmentError: null,

  getAllDepartmentPending: false,
  getAllDepartmentRes: null,
  getAllDepartmentError: null,

  getDepartmentListPending: false,
  getDepartmentListRes: null,
  getDepartmentListError: null,

  updateDepartmentPending: false,
  updateDepartmentRes: null,
  updateDepartmentError: null,

  deleteDepartmentPending: false,
  deleteDepartmentRes: null,
  deleteDepartmentError: null,
};
// add department
const handleAddDepartmentRequest = state => {
  return {
    ...state,
    ...{
      addDepartmentPending: true,
      addDepartmentRes: null,
      addDepartmentError: null,
    },
  };
};
const handleAddDepartmentSuccess = (state, payload) => {
  return {
    ...state,
    ...{
      addDepartmentPending: false,
      addDepartmentRes: payload,
      addDepartmentError: null,
    },
  };
};
const handleAddDepartmentError = (state, payload) => ({
  ...state,
  ...{
    addDepartmentPending: false,
    addDepartmentRes: null,
    addDepartmentError: payload,
  },
});


// get department 
const handleGetAllDepartmentRequest = state1 => {
  return {
    ...state1,
    ...{
      getAllDepartmentPending: true,
      getAllDepartmentRes: null,
      getAllDepartmentError: null,
    },
  };
};
const handleGetAllDepartmentSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getAllDepartmentPending: false,
      getAllDepartmentRes: payload,
      getAllDepartmentError: null,
    },
  };
};
const handleGetAllDepartmentError = (state1, payload) => ({
  ...state1,
  ...{
    getAllDepartmentPending: false,
    getAllDepartmentRes: null,
    getAllDepartmentError: payload,
  },
});

// get department List
const handleGetDepartmentListRequest = state1 => {
  return {
    ...state1,
    ...{
      getDepartmentListPending: true,
      getDepartmentListRes: null,
      getDepartmentListError: null,
    },
  };
};
const handleGetDepartmentListSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      getDepartmentListPending: false,
      getDepartmentListRes: payload,
      getDepartmentListError: null,
    },
  };
};
const handleGetDepartmentListError = (state1, payload) => ({
  ...state1,
  ...{
    getDepartmentListPending: false,
    getDepartmentListRes: null,
    getDepartmentListError: payload,
  },
});

// Update department 
const handleUpdateDepartmentRequest = state1 => {
  return {
    ...state1,
    ...{
      updateDepartmentPending: true,
      updateDepartmentRes: null,
      updateDepartmentError: null,
    },
  };
};
const handleUpdateDepartmentSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      updateDepartmentPending: false,
      updateDepartmentRes: payload,
      updateDepartmentError: null,
    },
  };
};
const handleUpdateDepartmentError = (state1, payload) => ({
  ...state1,
  ...{
    updateDepartmentPending: false,
    updateDepartmentRes: null,
    updateDepartmentError: payload,
  },
});

// Delete department 
const handleDeleteDepartmentRequest = state1 => {
  return {
    ...state1,
    ...{
      deleteDepartmentPending: true,
      deleteDepartmentRes: null,
      deleteDepartmentError: null,
    },
  };
};
const handleDeleteDepartmentSuccess = (state1, payload) => {
  return {
    ...state1,
    ...{
      deleteDepartmentPending: false,
      deleteDepartmentRes: payload,
      deleteDepartmentError: null,
    },
  };
};
const handleDeleteDepartmentError = (state1, payload) => ({
  ...state1,
  ...{
    deleteDepartmentPending: false,
    deleteDepartmentRes: null,
    deleteDepartmentError: payload,
  },
});

// clear existing data
const clearDepartmentStore = (state, payload) => {
  switch (payload) {
    case 'HANDLE_CLEAR_ADD_DEPARTMENT_STORE':
      return {
        ...state,
        ...{
          addDepartmentPending: false,
          addDepartmentRes: null,
          addDepartmentError: null,
        },
      };
    case 'HANDLE_CLEAR_UPDATE_DEPARTMENT_STORE':
      return {
        ...state,
        ...{
          updateDepartmentPending: false,
          updateDepartmentRes: null,
          updateDepartmentError: null,
        },
      };
      case 'HANDLE_CLEAR_DELETE_DEPARTMENT_STORE':
      return {
        ...state,
        ...{
          deleteDepartmentPending: false,
          deleteDepartmentRes: null,
          deleteDepartmentError: null,
        },
      };
    default:
      return state;
  }
};



export default createReducer(
  {
    [actions.addDepartmentRequest]: handleAddDepartmentRequest,
    [actions.addDepartmentSuccess]: handleAddDepartmentSuccess,
    [actions.addDepartmentError]: handleAddDepartmentError,

    [actions.getAllDepartmentRequest]: handleGetAllDepartmentRequest,
    [actions.getAllDepartmentSuccess]: handleGetAllDepartmentSuccess,
    [actions.getAllDepartmentError]: handleGetAllDepartmentError,

    [actions.getDepartmentListRequest]: handleGetDepartmentListRequest,
    [actions.getDepartmentListSuccess]: handleGetDepartmentListSuccess,
    [actions.getDepartmentListError]: handleGetDepartmentListError,

    [actions.updateDepartmentRequest]: handleUpdateDepartmentRequest,
    [actions.updateDepartmentSuccess]: handleUpdateDepartmentSuccess,
    [actions.updateDepartmentError]: handleUpdateDepartmentError,

    [actions.deleteDepartmentRequest]: handleDeleteDepartmentRequest,
    [actions.deleteDepartmentSuccess]: handleDeleteDepartmentSuccess,
    [actions.deleteDepartmentError]: handleDeleteDepartmentError,

    [actions.handleClearDepartmentStore]: clearDepartmentStore,
  },
  initialState,
);