import React from "react";


import {
  dashboardTotalAttendanceStatus,
  dashboardWishesCard,
} from "../utils/Constants.config";
import  {AnnouncementCard,AttendanceTrendCard,DashboardWishesCard,ExceedingCard,GreetingEmployeeCard,LeaveStaticsCard,MyTaskCard,PaySlipRequest,RequestPendingCard,TotalAttendanceAndLeaveCountStatus,TotalleavesCard,UpCommingHoliday,WeeklyAttendanceCard,WhoOnLeaveCard} from '../components/mainDashboard/index'

const MainDashboard = () => {
  return (
    <>
      <div className="cardBox mb-3 totalCount">
        <div className="row">
          <GreetingEmployeeCard />
          <div className="col-md-7">
            <div className="row px-md-1">
              {dashboardTotalAttendanceStatus.map((item,index) => (
                <TotalAttendanceAndLeaveCountStatus
                  icon={item?.icon}
                  key={index}
                  iconWidth={item?.iconWidth}
                  backgroundImg={item?.backgroundImg}
                  backgroundImgWidth={item?.backgroundImgWidth}
                  name={item?.name}
                  count={item?.count}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
      <div className="row mb-3">
        {dashboardWishesCard.map((item,index) => (
          <DashboardWishesCard
            title={item?.title}
            key={index}
            subTitle={item?.subTitle}
            btnText={item?.btnText}
            lottieImageSrc={item?.lottieImageSrc}
            lottieCustomeStyle={item?.lottieCustomeStyle}
          />
        ))}
      </div>
      <div className="row px-md-1">
        <div className="col-md-8 px-md-2">
          <div className="row px-md-1">
            <WeeklyAttendanceCard />
            <AttendanceTrendCard />
            <LeaveStaticsCard />
            <WhoOnLeaveCard />
            <TotalleavesCard />
            <PaySlipRequest />
            <UpCommingHoliday />
          </div>
        </div>
        <div className="col-md-4 px-md-2">
          <AnnouncementCard />
          <ExceedingCard />
          <MyTaskCard />
          <RequestPendingCard />
        </div>
      </div>
    </>
  );
};

export default MainDashboard;
