import React from "react";
import CalanderEvent from "./CalanderEvent";
import CalanderDays from "./CalanderDays";

const CalanderBody = ({ dateMatrix }) => {
  return (
    <div className="table-responsive">
      <table className="table m-0">
        <CalanderDays />
        <tbody>
          {dateMatrix.map((week, index) => (
            <tr key={index}>
              {week.map((date) => (
                <td key={date}>
                  <a
                    className="dateGroup"
                    data-bs-toggle="modal"
                    href="#selectModal"
                  >
                    <strong className={date === null ? "disabled" : ""}>
                      {date}
                    </strong>
                    {date != null && (
                      <div className="shiftGroup">
                        <CalanderEvent theam="greenColor" />
                        <CalanderEvent theam="redColor" />
                        <CalanderEvent theam="yelloColor" />
                        <CalanderEvent theam="plColor" />
                      </div>
                    )}
                  </a>
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CalanderBody;
