import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
} from 'redux-saga/effects';
import { qualificationActions as actions, notificationActions } from '../actions';
import { qualificationService } from '../services';

// add Department
function* handleAddQualificationRequest() {
    const action = yield take(actions.addQualificationRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
        res = yield call(qualificationService.addQualification, dataParams);
        if (res.isError) {
            yield put(actions.addQualificationError(res));
            yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
        } else {
            yield put(actions.addQualificationSuccess(res));
            yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
        }
    } catch (error) {
        yield put(actions.addQualificationError());
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
}

// Get Department
function* handleGetAllQualificationRequest() {
    const action = yield take(actions.getAllQualificationRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
        res = yield call(qualificationService.getAllQualification, dataParams);

        if (res.data.error) {
            yield put(actions.getAllQualificationError(res));

        } else {
            yield put(actions.getAllQualificationSuccess(res));
        }

    } catch (error) {
        yield put(actions.getAllQualificationError());
    }
}


// -----------Watchers-------------
function* handleAddQualificationWatcher() {
    yield takeEvery(
        actions.addQualificationRequest,
        handleAddQualificationRequest,
    );
}

function* handleGetQualificationWatcher() {
    yield takeEvery(
        actions.getAllQualificationRequest,
        handleGetAllQualificationRequest,
    );
}

function* rootQualificationSaga() {
    yield all([
        handleAddQualificationRequest(),
        handleAddQualificationWatcher(),

        handleGetAllQualificationRequest(),
        handleGetQualificationWatcher(),

    ]);
}

export default rootQualificationSaga;
