import { createReducer } from 'redux-act';
import actions from '../actions/loginActions';

const initialState = {
  isUserLoginPending: false,
  userLoginRes: null,
  userLoginResError: null,
};

const handleUserLoginRequest = state => {
    return {
      ...state,
      ...{
        isUserLoginPending: true,
        userLoginRes: null,
        userLoginResError: null,
      },
    };
  };
  const handleUserLoginSuccess = (state, payload) => {
    return {
      ...state,
      ...{
        isUserLoginPending: false,
        userLoginRes: payload,
        userLoginResError: null,
      },
    };
  };
  const handleUserLoginError = (state, payload) => ({
    ...state,
    ...{
      isUserLoginPending: false,
      userLoginRes: null,
      userLoginResError: payload,
    },
  });



  // handle clear login 
  const handleClearLoginStore = (state, payload) => {
    switch (payload) {
      case 'HANDLE_CLEAR_LOGIN_STORE':
        return {
          ...state,
          ...{
            isUserLoginPending: false,
            userLoginRes: null,
            userLoginResError: null,
          },
        };
     
      default:
        return state;
    }
  };

  export default createReducer(
    {
      [actions.userLoginRequest]: handleUserLoginRequest,
      [actions.userLoginSuccess]: handleUserLoginSuccess,
      [actions.userLoginError]: handleUserLoginError,

      // clear login store
      [actions.handleClearloginStore]:handleClearLoginStore,
    },
    initialState,
  );