import { createAction } from 'redux-act';


// get Department action
const getEmployeeForReportingManagerRequest = createAction('GET_EMPLOYEE_FOR_REPORTING_MANAGER_REQUEST', data => data);
const getEmployeeForReportingManagerSuccess = createAction('GET_EMPLOYEE_FOR_REPORTING_MANAGER_SUCCESS', data => data);
const getEmployeeForReportingManagerError = createAction('GET_EMPLOYEE_FOR_REPORTING_MANAGER_ERROR');

// get All Employee Action
const getAllEmployeeRequest = createAction('GET_ALL_EMPLOYEE_REQUEST', data => data);
const getAllEmployeeSuccess = createAction('GET_ALL_EMPLOYEE_SUCCESS', data => data);
const getAllEmployeeError = createAction('GET_ALL_EMPLOYEE_ERROR');

// clear existing Department action
const handleClearDepartmentStore = createAction('HANDLE_CLEAR_EMPLOYEE__STORE', data => data);
// const toastMsgAction = createAction('SUCCESS', data=> data);

export default {

    getEmployeeForReportingManagerRequest,
    getEmployeeForReportingManagerSuccess,
    getEmployeeForReportingManagerError,

    getAllEmployeeRequest,
    getAllEmployeeSuccess,
    getAllEmployeeError,

    handleClearDepartmentStore,

}