// clear existing data
const clearEmailTemplateStore = (state, payload) => {
  switch (payload) {
    case 'HANDLE_CLEAR_ADD_EMAILTYPE_STORE':
      return {
        ...state,
        ...{
          addEmailTypePending: false,
          addEmailTypeRes: null,
          addEmailTypeError: null,
        },
      };
    case 'HANDLE_CLEAR_GET_EMAIL_TYPE_STORE':
      return {
        ...state,
        ...{
          getEmailTypePending: false,
          getEmailTypeRes: null,
          getEmailTypeError: null,
        },
      };
    case 'HANDLE_CLEAR_ADD_EMAIL_TEMPLATE_STORE':
      return {
        ...state,
        ...{
          addEmailTemplatePending: false,
          addEmailTemplateRes: null,
          addEmailTemplateError: null,
        },
      };
    case 'HANDLE_CLEAR_GET_EMAIL_TEMPLATE_STORE':
      return {
        ...state,
        ...{
          addEmailTemplatePending: false,
          addEmailTemplateRes: null,
          addEmailTemplateError: null,
        },
      };

    case 'HANDLE_CLEAR_DELETE_EMAIL_TEMPLATE_STORE':
      return {
        ...state,
        ...{
          deleteEmailTemplatePending: false,
          deleteEmailTemplateRes: null,
          deleteEmailTemplateError: null,
        },
      };
    case 'HANDLE_CLEAR_ADD_ACTION_TEMPLATE_STORE':
      return {
        ...state,
        ...{
          addActionTemplatePending: false,
          addActionTemplateRes: null,
          addActionTemplateError: null
        },
      };
    case 'HANDLE_CLEAR_DELETE_ACTION_TEMPLATE_STORE':
      return {
        ...state,
        ...{
          DeleteActionTemplatePending: false,
          DeleteActionTemplateRes: null,
          DeleteActionTemplateError: null,
        },
      };
    default:
      return state;
  }
};



export default createReducer(
  {
    [actions.addEmailTypeRequest]: handleAddEmailTypeRequest,
    [actions.addEmailTypeSuccess]: handleAddEmailTypetSuccess,
    [actions.addEmailTypeError]: handleAddEmailTypeError,

    [actions.getEmailTypeRequest]: handleGetEmailtypeRequest,
    [actions.getEmailTypeSuccess]: handleGetEmailtypeSuccess,
    [actions.getEmailTypeError]: handleGetEmailtypeError,

    [actions.addEmailTemplateRequest]: handleEmailTemplateRequest,
    [actions.addEmailTemplateSuccess]: handleEmailTemplateSuccess,
    [actions.addEmailTemplateError]: handleEmailTemplateError,

    [actions.getEmailTemplateRequest]: handleGetEmailTemplateRequest,
    [actions.getEmailTemplateSuccess]: handleGetEmailTemplateSuccess,
    [actions.getEmailTemplateError]: handleGetEmailTemplateError,

    [actions.updateEmailTemplateRequest]: handleUpdateEmailTemplateRequest,
    [actions.updateEmailTemplateSuccess]: handleUpdateEmailTemplateSuccess,
    [actions.updateEmailTemplateError]: handleUpdateEmailTemplateError,

    [actions.deleteEmailTemplateRequest]: handleDeleteEmailTemplateRequest,
    [actions.deleteEmailTemplateSuccess]: handleDeleteEmailTemplateSuccess,
    [actions.deleteEmailTemplateError]: handleDeleteEmailTemplateError,

    [actions.RemoveImageRequest]: handleRemoveImageRequest,
    [actions.RemoveImageSuccess]: handleRemoveImageSuccess,
    [actions.RemoveImageError]: handleRemoveImageError,

    [actions.AddActionTemplateRequest]: handleAddActionTemplateRequest,
    [actions.AddActionTemplateSuccess]: handleAddActionTemplateSuccess,
    [actions.AddActionTemplateError]: handleAddActionTemplateError,

    [actions.getActionTemplateRequest]: handleGetActionTemplateRequest,
    [actions.getActionTemplateSuccess]: handleGetActionTemplateSuccess,
    [actions.getActionTemplateError]: handleGetActionTemplateError,

    [actions.UpdataActionTemplateRequest]: handleUpdateActionTemplateRequest,
    [actions.UpdataActionTemplateSuccess]: handleUpdateActionTemplateSuccess,
    [actions.UpdataActionTemplateError]: handleUpdateActionTemplateError,

    [actions.DeleteActionTemplateRequest]: handleDeleteActionTemplateRequest,
    [actions.DeleteActionTemplateSuccess]: handlDeleteActionTemplateSuccess,
    [actions.DeleteActionTemplateError]: handleDeleteActionTemplateError,

    [actions.GetActionTypeTemplateRequest]: handleGetActionTypeTemplateRequest,
    [actions.GetActionTypeemplateSuccess]: handlGetActionTypeTemplateSuccess,
    [actions.GetActionTypeemplateError]: handleGetActionTypeTemplateError,

    [actions.handleClearEmailTemplateStore]: clearEmailTemplateStore,
  },
  initialState,
);