import React from 'react'

const WeeklyAttendanceCard = () => {
  return (
      <div className="col-md-12 px-md-2 mb-3">
      <div className="cardBox h-100">
        <div className="cardHead">
          <h3>Weekly Attendance Trend</h3>
          <p>Employee Attendance Statistics</p>
        </div>
        <div id="weeklyChart" style={{ height: "220px" }} />
      </div>
    </div>
  )
}

export default WeeklyAttendanceCard