import { baseUrls, apiUrls } from "../utils/Endpoints.config";
import {
  postRequest,
  callApi,
} from "../core/resource/services/apiService";
import { getDataFromLocalstorage, removeDataFromLocalstorage } from './AppHelper';

const header = {
  authorization: "",
};
let additionalParams = {
  isInterceptors: false,
  isPassword: false
}

const getAccessTokenData = () => {
  return getDataFromLocalstorage("authtoken");
}
const getClientTypeAndId = (params) => {
  const clientId = params?.email ? window.btoa(params?.email) : 'g123456'
  return {
    client_id: clientId,
    client_type: "web",
  };
};


const logoutUser = async () => {
  // const accessTokenData = getAccessTokenData();
  const accessTokenData = '';

  const url = `${baseUrls.mainUrl}${apiUrls.account.logout}`;
  const options = postRequest({
    account_id: accessTokenData?.account_id || '',
    // token_type: "access",
  });

  header.authorization = accessTokenData?.token || '';
  const res = await callApi(url, options, header);
  removeDataFromLocalstorage('access_token_data');
  removeDataFromLocalstorage('user_data');
  
  // redirect to login page
  window.location.replace('/');
  
  return res;
};

export {
  getAccessTokenData,
  getClientTypeAndId,
  logoutUser,
};
