import { createReducer } from 'redux-act';
import actions from '../actions/dropdownMasterActions';

const initialState = {
    getFilteredDropdownDataPending: false,
    getFilteredDropdownDataRes: null,
    getFilteredDropdownDataError: null,

};


// get Training Certificates
const handleGetFilteredDropdownDataRequest = state1 => {
    return {
        ...state1,
        ...{
            getFilteredDropdownDataPending: true,
            getFilteredDropdownDataRes: null,
            getFilteredDropdownDataError: null,
        },
    };
};
const handleGetFilteredDropdownDataSuccess = (state1, payload) => {
    return {
        ...state1,
        ...{
            getFilteredDropdownDataPending: false,
            getFilteredDropdownDataRes: payload,
            getFilteredDropdownDataError: null,
        },
    };
};
const handleGetFilteredDropdownDataError = (state1, payload) => ({
    ...state1,
    ...{
        getFilteredDropdownDataPending: false,
        getFilteredDropdownDataRes: null,
        getFilteredDropdownDataError: payload,
    },
});

export default createReducer(
    {
        [actions.getFilteredDropdownDataRequest]: handleGetFilteredDropdownDataRequest,
        [actions.getFilteredDropdownDataSuccess]: handleGetFilteredDropdownDataSuccess,
        [actions.getFilteredDropdownDataError]: handleGetFilteredDropdownDataError,
        
    },
    initialState,
);