import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
  } from 'redux-saga/effects';
  import { shiftManagementAction as actions,notificationActions } from '../actions';
  import { shiftManagementService } from '../services';

  // add Shift
  function* handleAddShiftRequest() {
    const action = yield take(actions.addShiftRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(shiftManagementService.addShift, dataParams);
      if (res.isError) {
        yield put(actions.addShiftError(res));     
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
      } else {
        yield put(actions.addShiftSuccess(res));  
        yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
      }
    } catch (error) {
      yield put(actions.addShiftError());
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
  }
  
 // Get Shift
  function* handleGetAllShiftRequest() {
    const action = yield take(actions.getAllShiftRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(shiftManagementService.getAllShift,dataParams);

      if (res.data.error) {
        yield put(actions.getAllShiftError(res));
        
      } else {
        yield put(actions.getAllShiftSuccess(res));
      }

    } catch (error) {
      yield put(actions.getAllShiftError());
    }
  }

