  // -----------Watchers-------------
  function* handleAddShiftWatcher() {
    yield takeEvery(
      actions.addShiftRequest,
      handleAddShiftRequest,
    );
  }
  
  function* handleGetShiftWatcher() {
    yield takeEvery(
      actions.getAllShiftRequest,
      handleGetAllShiftRequest,
    );
  }

  function* handleUpdateShiftWatcher() {
    yield takeEvery(
      actions.updateShiftRequest,
      handleUpdateShiftRequest,
    );
  }

  function* handleDeleteShiftWatcher() {
    yield takeEvery(
      actions.deleteShiftRequest,
      handleDeleteShiftRequest,
    );
  }

  function* handleAddShiftConfigurationWatcher() {
    yield takeEvery(
      actions.addShiftConfigurationRequest,
      handleAddShiftConfigurationRequest,
    );
  }

  function* handleGetShiftConfigurationWatcher() {
    yield takeEvery(
      actions.getShiftConfigurationRequest,
      handleGetShiftConfigurationRequest,
    );
  }


  function* handleAddShiftCalendarWatcher() {
    yield takeEvery(
      actions.addShiftCalendarRequest,
      handleAddShiftCalendarRequest,
    );
  }
  
  function* handleGetShiftCalendarWatcher() {
    yield takeEvery(
      actions.getAllShiftCalendarRequest,
      handleGetAllShiftCalendarRequest,
    );
  }

  function* handleUpdateShiftCalendarWatcher() {
    yield takeEvery(
      actions.updateShiftCalendarRequest,
      handleUpdateShiftCalendarRequest,
    );
  }

  function* handleDeleteShiftCalendarWatcher() {
    yield takeEvery(
      actions.deleteShiftCalendarRequest,
      handleDeleteShiftCalendarRequest,
    );
  }

 
  
  function* rootShiftSaga() {
    yield all([
        handleAddShiftRequest(),
        handleAddShiftWatcher(),

        handleGetAllShiftRequest(),
        handleGetShiftWatcher(),

        handleUpdateShiftRequest(),
        handleUpdateShiftWatcher(),

        handleDeleteShiftRequest(),
        handleDeleteShiftWatcher(),

        handleAddShiftConfigurationRequest(),
        handleAddShiftConfigurationWatcher(),

        handleGetShiftConfigurationRequest(),
        handleGetShiftConfigurationWatcher(),

        handleAddShiftCalendarRequest(),
        handleAddShiftCalendarWatcher(),

        handleGetAllShiftCalendarRequest(),
        handleGetShiftCalendarWatcher(),

        handleUpdateShiftCalendarRequest(),
        handleUpdateShiftCalendarWatcher(),

        handleDeleteShiftCalendarRequest(),
        handleDeleteShiftCalendarWatcher(),
    ]);
  }
  
  export default rootShiftSaga;
  