import moment from 'moment-timezone';
import { decryptValue } from './common';

const userStorageKey = "user_info";
const defaultTimezone = 'Asia/Kolkata';

const setDataOnLocal = (data, key) => {
  const storageKey = key || userStorageKey;
  sessionStorage.setItem(storageKey, JSON.stringify(data));
};
const getDataOnLocal = (key) => {
  const storageKey = key || userStorageKey;
  const data = sessionStorage.getItem(storageKey);
  return data ? JSON.parse(data) : null;
};
const removeDataOnLocal = (key) => {
  const storageKey = key || userStorageKey;
  return sessionStorage.removeItem(storageKey);
};

// local storage
const setDataOnLocalstorage = (data, key) => {
  const storageKey = key || userStorageKey;
  localStorage.setItem(storageKey, JSON.stringify(data));
};
const getDataFromLocalstorage = (key) => {
  const storageKey = key || userStorageKey;
  const data = localStorage.getItem(storageKey);
  return data ? JSON.parse(data) : null;
};

const getLocalDataAsObject = (keyAsString) => {
  //  alert('getLocalDataAsObject=', keyAsString + '=' + localStorage.getItem(keyAsString))
  if (localStorage.getItem(keyAsString) !== undefined) {
      var objData = undefined;
      try {
          var data;
          if(keyAsString == 'user'){
            let encryptedData = localStorage.getItem(keyAsString);
              let encryptString= JSON.parse(encryptedData)
              data = decryptValue(encryptString);
          }else{
              data = localStorage.getItem(keyAsString);
          }
          if (typeof data !== "undefined" && data !== "undefined") {
              objData = JSON.parse(data);
          }
      } catch (error) {
          // console.log(' error getLocalDataAsObject : ', error['message']);
      }
      return objData;
  }
}



const removeDataFromLocalstorage = (key) => {
  const storageKey = key || userStorageKey;
  return localStorage.removeItem(storageKey);
};

const convertToFile = (file, convertTo = "base64") => {
  return new Promise((resolve, reject) => {
    // Make new FileReader
    let reader = new FileReader();

    // Convert the file to base64 text
    reader.readAsDataURL(file);

    // on reader load somthing...
    reader.onload = () => {
      resolve(reader.result);
    };
    reader.onerror = (error) => {
      reject(error);
    };
  });
};
const convertValue = (val, convertTo = "float") => {
  if (val) {
    if (convertTo === "float") return parseFloat(val, 10).toFixed(2);
  }
  return val;
};
const convertToDate = (dateTimestamp, format = 'DD/MM/YYYY') => { 
  return moment.tz(dateTimestamp, defaultTimezone).format(format);
};
const convertToTS = (dateTimestamp, format = 'DD/MM/YYYY') => { 
  console.log({dateTimestamp});
  return moment(dateTimestamp).format(format);
};
export {
  setDataOnLocal,
  getDataOnLocal,
  removeDataOnLocal,
  convertToFile,
  convertValue,
  convertToDate,
  convertToTS,
  setDataOnLocalstorage,
  getDataFromLocalstorage,
  removeDataFromLocalstorage,
  getLocalDataAsObject,
};
