import { createAction } from 'redux-act';
// add Department action
const addDepartmentRequest = createAction('ADD_DEPARTMENT_REQUEST', data => data);
const addDepartmentSuccess = createAction('ADD_DEPARTMENT_SUCCESS', data => data);
const addDepartmentError = createAction('ADD_DEPARTMENT_ERROR');

// get Department action
const getAllDepartmentRequest = createAction('GET_ALL_DEPARTMENT_REQUEST', data => data);
const getAllDepartmentSuccess = createAction('GET_ALL_DEPARTMENT_SUCCESS', data => data);
const getAllDepartmentError = createAction('GET_ALL_DEPARTMENT_ERROR');

// get Department list
const getDepartmentListRequest = createAction('GET_DEPARTMENT_LIST_REQUEST', data => data);
const getDepartmentListSuccess = createAction('GET_DEPARTMENT_LIST_SUCCESS', data => data);
const getDepartmentListError = createAction('GET_DEPARTMENT_LIST_ERROR');


// Update Department action
const updateDepartmentRequest = createAction('Update_DEPARTMENT_REQUEST', data => data);
const updateDepartmentSuccess = createAction('Update_DEPARTMENT_SUCCESS', data => data);
const updateDepartmentError = createAction('Update_DEPARTMENT_ERROR');

// Delete Department action
const deleteDepartmentRequest = createAction('Delete_DEPARTMENT_REQUEST', data => data);
const deleteDepartmentSuccess = createAction('Delete_DEPARTMENT_SUCCESS', data => data);
const deleteDepartmentError = createAction('Delete_DEPARTMENT_ERROR');

// clear existing Department action
const handleClearDepartmentStore = createAction('HANDLE_CLEAR_DEPARTMENT_STORE',data => data);
// const toastMsgAction = createAction('SUCCESS', data=> data);

export default {
    addDepartmentRequest,
    addDepartmentSuccess,
    addDepartmentError,

    getAllDepartmentRequest,
    getAllDepartmentSuccess,
    getAllDepartmentError,

    getDepartmentListRequest,
    getDepartmentListSuccess,
    getDepartmentListError,

    updateDepartmentRequest,
    updateDepartmentSuccess,
    updateDepartmentError,

    deleteDepartmentRequest,
    deleteDepartmentSuccess,
    deleteDepartmentError,
    
    handleClearDepartmentStore,
    // toastMsgAction,
    
}