import React from "react";
import Button from "../commanComponent/Button";

const DashboardWishesCard = ({
  title,
  subTitle,
  btnText,
  lottieImageSrc,
  lottieCustomeStyle,
}) => {
  return (
    <div className="col-md-3 pe-md-2">
      <div className="birthdayBx">
        <h6>{title}</h6>
        <p>{subTitle}</p>

        <Button btnClassName="btn btn-primary text-white">{btnText}</Button>

        <div className="cornerlottie" style={lottieCustomeStyle}>
          <lottie-player
            src={lottieImageSrc}
            background="transparent"
            speed={1}
            loop
            autoPlay
          ></lottie-player>
        </div>
      </div>
    </div>
  );
};

export default DashboardWishesCard;
