import React from 'react'

const AttendanceTrendCard = () => {
  return (
      <div className="col-md-5 px-md-2 mb-3">
      <div className="cardBox h-100">
        <div className="cardHead pb-1">
          <h3>Attendance Trend</h3>
          <p>Employee Attendance Statistics</p>
        </div>
        <div className="atttendanceChart position-relative">
          <div
            id="attendanceTrend"
            style={{ width: "200px", height: "260px", margin: "auto" }}
          />
          <strong className="attendanceTxt">88%</strong>
        </div>
      </div>
    </div>
  )
}

export default AttendanceTrendCard