import { encryptValue } from "../../../utils/common";
import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";


  const userLogin = (params) => {
    const { axiosInstance,config} = getAxiosConfig();
    let {email,password}=params
    let param = {email}
    config.headers["password"] =encryptValue(password)
    const response = axiosInstance({
      url: `${baseUrls.main}/${apiUrls.auth.login}`,
      method: "POST",
      data: param,
      headers: config.headers||null,
      mode: "no-cors",
    })
      .then((res) => {
        return res;
      })
      .catch((error) => {
        return getFomatedError(error.response.data);
      });
    return response;
  };

  export default {
    userLogin,
  }