import * as React from 'react';
import { useSelector } from 'react-redux'
import Spinner from 'react-bootstrap/Spinner';

export default function CustomSpinner(props) {
  const { isShow, size } = props;
  //const isLoginLoader = useSelector(state => state.auth.isLoginPending);

  let loader = isShow || false;
  let divStyles = {}
  let loaderStyles = {}

  if (loader) {
    loader = true
  }

  const spinnerSize = size ||''

  switch (spinnerSize) {
    case 'sm': // for button
      loaderStyles = {
        width: "22px",
        height: "22px",
        verticalAlign: "-.125em",
        border: ".20em solid currentColor",
        borderRightColor: "transparent",
      };
      break;
    case 'md':
      loaderStyles = {
        marginTop: "19px !important",
        marginBottom: "19px !important",
        width: "50px !important",
        height: "50px !important",
        zIndex: "2"
      }
      break;
    case 'xl': // for table
      divStyles = {
        height: "10%",
        display: "flex",
        position: "relative",
        inset: "0% 0px 0px 166%",
        zIndex: "2",
        justifyContent: "center",
        margin: "40px"

      }
      loaderStyles = {
        width: "60px",
        height: "60px",
        Zindex: "2"
      }
      break;
    case 'lg': 
      divStyles = {
        height: '10%',
        display: 'flex',
        position: 'absolute',
        left: '50%',
        right: 0,
        bottom: 0,
        top: '25%',
        zIndex: 2
      };
      loaderStyles = {
        width: '100px',
        height: '100px',
        zIndex: 2
      };
      break;
    default:

  }


  return (
    (loader) && <div style={divStyles}>
      <Spinner style={loaderStyles} {...props} />
    </div>

  );
}
