import React from 'react'
import Button from '../../commanComponent/Button'

const YourPoints = () => {
  return (
      <div className="cardBox mb-3">
      <div className="cardHead">
        <h3>Your Points</h3>
        <p className="fw-semibold text-black">
          Kudos point summary as on 26 Dec 2023
        </p>
      </div>
      <ul className="yourPoints">
        <li>
          <span>Previous Balance</span> <b>4397 Pts</b>
        </li>
        <li>
          <span>Earned</span> <b>4397 Pts</b>
        </li>
        <li>
          <span>Redeemed</span> <b>200 Pts </b>
        </li>
        <li>
          <span>Available Points</span> <b>4397</b>
        </li>
        <li>
          <span>Point expiring by this month</span> <b>100 Pts</b>
        </li>
      </ul>
      <div className="d-flex justify-content-center">
       <Button btnClassName={"btn-org-gradient w-100 text-center"}>Redeem Now</Button>
      </div>
    </div>
  )
}

export default YourPoints