import React from "react";
import Image from "../commanComponent/Image";

const TotalAttendanceAndLeaveCountStatus = ({
  icon,
  iconWidth,
  backgroundImg,
  backgroundImgWidth,
  name,
  count,
}) => {
  return (
    <div className="col-md-4 px-md-2">
      <div className="totalBx h-100">
        <figure>
          <Image imgSrc={icon} width={iconWidth}></Image>
        </figure>
        <h3>
          {name} <span>{count}</span>
        </h3>
        <Image
          imgSrc={backgroundImg}
          imgClassName={"cornerLine"}
          width={backgroundImgWidth}
        />
      </div>
    </div>
  );
};

export default TotalAttendanceAndLeaveCountStatus;
