import { combineReducers } from 'redux';
import {
  commonReducers,
  loginReducers,
  departmentReducers,
  notificationReducers,
  designationReducers,
  sidebarReducers,
  qualificationReducers,
  trainingCertificateReducers,
  dropdownMasterReducers,
  positionReducers,
  mrfReducers,
  mrfInterviewReducers,
  locationReducers,
  employeeReducers,
  shiftManagementReducers,
  emailTemplateReducers
} from './reducers';


const appReducer = combineReducers({
  notification: notificationReducers,
  common: commonReducers,
  login: loginReducers,
  department:departmentReducers,
  designation:designationReducers,
  notifications: notificationReducers,
  emailtemplate: emailTemplateReducers,
  sidebar:sidebarReducers,
  shift:shiftManagementReducers,
  department: departmentReducers,
  designation: designationReducers,
  sidebar: sidebarReducers,
  qualification: qualificationReducers,
  trainingCertificate: trainingCertificateReducers,
  dropdownMaster: dropdownMasterReducers,
  position:positionReducers,
  mrf:mrfReducers,
  location:locationReducers,
  employee :employeeReducers,
  mrfInterview :mrfInterviewReducers
});

const rootReducer = (state, action) => {
  const { type, payload } = action;
  let newState;

  if (type === 'APP_INIT') {
    newState = { ...state, internal: {} };
  } else if (type === 'RELOAD_STORE') {
    newState = { ...payload };
  } else if (type === 'CLEAR_INTERNAL_STORE') {
    newState = { routing: { ...state.routing } };
  }

  return appReducer(newState || state, action);
};

export default rootReducer;
