import {
  take,
  put,
  call,
  all,
  takeLatest,
  takeEvery,
} from 'redux-saga/effects';
import { mrfActions as actions, notificationActions } from '../actions';
import { mrfService } from '../services';

//  Create update mrf
function* handleCreateUpdateMrfRequest() {
  const action = yield take(actions.createUpdateMrfRequest.getType());
  const dataParams = action.payload;
  let res;
  try {
    res = yield call(mrfService.createUpdateMrf, dataParams);
    if (res.isError) {
      yield put(actions.createUpdateMrfError(res));
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    } else {
      yield put(actions.createUpdateMrfSuccess(res));
      yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
    }
  } catch (error) {
    yield put(actions.createUpdateMrfError());
    yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
  }
}


