// -----------Watchers-------------
function* handleCreateUpdateMrfWatcher() {
  yield takeEvery(
    actions.createUpdateMrfRequest,
    handleCreateUpdateMrfRequest,
  );
}

function* handleGetInterviewerListWatcher() {
  yield takeEvery(
    actions.getInterviewerListRequest,
    handleGetInterviewerListRequest,
  );
}

function* handleGetMrfDataByIdWatcher() {
  yield takeEvery(
    actions.getMrfDataByIdRequest,
    handleGetMrfDataByIdRequest,
  );
}

function* handleSubmitDraftMrfWatcher() {
  yield takeEvery(
    actions.submitDraftMrfRequest,
    handleSubmitDraftMrfRequest,
  );
}

function* handleGetAllUserMrfWatcher() {
  yield takeEvery(
    actions.getAllUserMrfRequest,
    handleGetAllUserMrfRequest,
  );
}


function* handleGetMrfDashboardAndHierarchyWatcher() {
  yield takeEvery(
    actions.getMrfDashboardAndHierarchyRequest,
    handleGetMrfDashboardAndHierarchyRequest,
  );
}

function* handleGetMrfForApprovalDataWatcher() {
  yield takeEvery(
    actions.getMrfForApprovalDataRequest,
    handleGetMrfForApprovalDataRequest,
  );
}

function* handleApproveRejectMrfWatcher() {
  yield takeEvery(
    actions.approveRejectMrfRequest,
    handleApproveRejectMrfRequest,
  );
}





function* rootMrfSaga() {
  yield all([
    handleCreateUpdateMrfRequest(),
    handleCreateUpdateMrfWatcher(),

    handleGetInterviewerListRequest(),
    handleGetInterviewerListWatcher(),

    handleGetMrfDataByIdRequest(),
    handleGetMrfDataByIdWatcher(),

    handleSubmitDraftMrfRequest(),
    handleSubmitDraftMrfWatcher(),

    handleGetAllUserMrfRequest(),
    handleGetAllUserMrfWatcher(),

    handleGetMrfDashboardAndHierarchyRequest(),
    handleGetMrfDashboardAndHierarchyWatcher(),

    handleGetMrfForApprovalDataRequest(),
    handleGetMrfForApprovalDataWatcher(),

    
    handleApproveRejectMrfRequest(),
    handleApproveRejectMrfWatcher(),



  ]);
}

export default rootMrfSaga;
