import React, { Children } from 'react'

const Button = ({btnClassName,customeStyle,onClickFun,children,dataBsTarget, dataBsToggle}) => {
  return (
   <button className={btnClassName} style={customeStyle} onClick={onClickFun} data-bs-target={dataBsTarget}  data-bs-toggle={dataBsToggle} >{children}</button>
  )
}

export default Button