import { baseUrls, apiUrls } from "../../../utils/Endpoints.config";
import { getAxiosConfig, getFomatedError } from "../services/apiService";

//add Qualification
const addQualification = (params) => {
    const { axiosInstance, config } = getAxiosConfig();
    const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.admin.addQualification}`,
        method: "POST",
        data: params,
        headers: config.headers || null,
        mode: "no-cors",
    })
        .then((res) => {
            return res;
        })
        .catch((error) => {
            return getFomatedError(error?.response?.data);
        });
    return response;
};

// get Qualification
const getAllQualification = (params) => {
    const { axiosInstance, config } = getAxiosConfig();
    const response = axiosInstance({
        url: `${baseUrls.main}/${apiUrls.admin.getAllQualification}`,
        method: "GET",
        params: params,
        headers: config.headers || null,
        mode: "no-cors",
    })
        .then((res) => {
            return res;
        })
        .catch((error) => {
            return getFomatedError(error);
        });
    return response;
};



export default {
    addQualification,
    getAllQualification,
}