import React from 'react'

const ExceedingCard = () => {
  return (
      <div className="cardBox mb-3">
      <div className="cardHead pb-2">
        <h3>Exceeding Corrections</h3>
      </div>
      <div className="excCorrection">
        <strong>
          03 <span>Overall Corrections</span>
        </strong>
        <strong>
          04<span>Monthly correction limit</span>
        </strong>
      </div>
    </div>
  )
}

export default ExceedingCard