import {
    take,
    put,
    call,
    all,
    takeLatest,
    takeEvery,
  } from 'redux-saga/effects';
  import { departmentAction as actions,notificationActions } from '../actions';
  import { DepartmentService } from '../services';

  // add Department
  function* handleAddDepartmentRequest() {
    const action = yield take(actions.addDepartmentRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(DepartmentService.addDepartment, dataParams);
      if (res.isError) {
        yield put(actions.addDepartmentError(res));     
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
      } else {
        yield put(actions.addDepartmentSuccess(res));  
        yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
      }
    } catch (error) {
      yield put(actions.addDepartmentError());
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
  }
  
 // Get Department
  function* handleGetAllDepartmentRequest() {
    const action = yield take(actions.getAllDepartmentRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(DepartmentService.getAllDepartment,dataParams);

      if (res.data.error) {
        yield put(actions.getAllDepartmentError(res));
        
      } else {
        yield put(actions.getAllDepartmentSuccess(res));
      }

    } catch (error) {
      yield put(actions.getAllDepartmentError());
    }
  }

  //Get Department List
  function* handleGetDepartmentListRequest() {
    const action = yield take(actions.getDepartmentListRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(DepartmentService.getDepartmentList,dataParams);

      if (res.data.error) {
        yield put(actions.getDepartmentListError(res));
        
      } else {
        yield put(actions.getDepartmentListSuccess(res));
      }

    } catch (error) {
      yield put(actions.getDepartmentListError());
    }
  }

// Update Department
  function* handleUpdateDepartmentRequest() {
    const action = yield take(actions.updateDepartmentRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(DepartmentService.updateDepartment,dataParams);
      if (res.isError) {
        yield put(actions.updateDepartmentError(res));
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
      } else {
        yield put(actions.updateDepartmentSuccess(res));
        yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
      }
    } catch (error) {
      yield put(actions.updateDepartmentError());
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
  }
// delete Department
  function* handleDeleteDepartmentRequest() {
    const action = yield take(actions.deleteDepartmentRequest.getType());
    const dataParams = action.payload;
    let res;
    try {
      res = yield call(DepartmentService.deleteDepartment,dataParams);
      if (res.isError) {
        yield put(actions.deleteDepartmentError(res));
        yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
      } else {
        yield put(actions.deleteDepartmentSuccess(res));
        yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
      }
    } catch (error) {
      yield put(actions.deleteDepartmentError());
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
    }
  }


  // -----------Watchers-------------
  function* handleAddDepartmentWatcher() {
    yield takeEvery(
      actions.addDepartmentRequest,
      handleAddDepartmentRequest,
    );
  }
  
  function* handleGetDepartmentWatcher() {
    yield takeEvery(
      actions.getAllDepartmentRequest,
      handleGetAllDepartmentRequest,
    );
  }

  function* handleGetDepartmentListWatcher() {
    yield takeEvery(
      actions.getDepartmentListRequest,
      handleGetDepartmentListRequest,
    );
  }

  function* handleUpdateDepartmentWatcher() {
    yield takeEvery(
      actions.updateDepartmentRequest,
      handleUpdateDepartmentRequest,
    );
  }

  function* handleDeleteDepartmentWatcher() {
    yield takeEvery(
      actions.deleteDepartmentRequest,
      handleDeleteDepartmentRequest,
    );
  }

 
  
  function* rootDepartmentSaga() {
    yield all([
        handleAddDepartmentRequest(),
        handleAddDepartmentWatcher(),

        handleGetAllDepartmentRequest(),
        handleGetDepartmentWatcher(),

        handleGetDepartmentListRequest(),
        handleGetDepartmentListWatcher(),

        handleUpdateDepartmentRequest(),
        handleUpdateDepartmentWatcher(),

        handleDeleteDepartmentRequest(),
        handleDeleteDepartmentWatcher(),
    ]);
  }
  
  export default rootDepartmentSaga;
  