import React from "react";
import LeaveCountHeader from "../components/attendanceDashboard/LeaveCountHeader";
import ApplyLeave from "../components/attendanceDashboard/ApplyLeave";
import Table from "../components/Table";

const LeaveManagement = () => {
  const tableTabs = [
    {
      name: "Attendance Correction",
      onClickFun: () => {},
      id: "AttecorrectionTab",
    },
    { name: "Web Attendance", onClickFun: () => {}, id: "webattenTabs" },
    { name: "Leaves", onClickFun: () => {}, id: "leaveRequestTabs" },
    { name: "Comp-off", onClickFun: () => {}, id: "coffTabs" },
    { name: "Tour", onClickFun: () => {}, id: "tourTabs" },
  ];
  return (
    <div className="ticketScoreSection">
      <LeaveCountHeader />
      <ApplyLeave />
      <Table tableName="My Request" tabletabs={tableTabs} />
    </div>
  );
};

export default LeaveManagement;
