import {
  take,
  put,
  call,
  all,
  takeLatest,
  takeEvery,
} from 'redux-saga/effects';
import { mrfInterviewAction as actions, notificationActions } from '../actions';
import { mrfInterviewService } from '../services';



// Get Department
function* handleGetAssignedInterviewRequest() {
  const action = yield take(actions.getAssignedInterviewRequest.getType());
  const dataParams = action.payload;
  let res;
  try {
    res = yield call(mrfInterviewService.getAssignedInterview, dataParams);

    if (res.data.error) {
      yield put(actions.getAssignedInterviewError(res));

    } else {
      yield put(actions.getAssignedInterviewSuccess(res));
    }

  } catch (error) {
    yield put(actions.getAssignedInterviewError());
  }
}

// get Question By Interview Stage
function* handleGetQuestionByInterviewStageRequest() {
  const action = yield take(actions.getQuestionByInterviewStageRequest.getType());
  const dataParams = action.payload;
  let res;
  try {
    res = yield call(mrfInterviewService.getQuestionByInterviewStage, dataParams);

    if (res.data.error) {
      yield put(actions.getQuestionByInterviewStageError(res));

    } else {
      yield put(actions.getQuestionByInterviewStageSuccess(res));
    }

  } catch (error) {
    yield put(actions.getQuestionByInterviewStageError());
  }
}

//fill Feedback Form
function* handleFillFeedbackFormRequest() {
  const action = yield take(actions.fillFeedbackFormRequest.getType());
  const dataParams = action.payload;
  let res;
  try {
    res = yield call(mrfInterviewService.fillFeedbackForm, dataParams);

    if (res.data.error) {
      yield put(actions.fillFeedbackFormError(res));
      yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));

    } else {
      yield put(actions.fillFeedbackFormSuccess(res));
      yield put(notificationActions.openSnackbar({ text: res?.data?.message || res?.errorMessage, alertType: 'success' }));
      
    }
    
  } catch (error) {
    yield put(actions.fillFeedbackFormError());
    yield put(notificationActions.openSnackbar({ text: res?.errorMsg || res?.errorMessage, alertType: 'error' }));
  }
}


// get Interview Details By Id
function* handleGetInterviewDetailsByIdRequest() {
  const action = yield take(actions.getInterviewDetailsByIdRequest.getType());
  const dataParams = action.payload;
  let res;
  try {
    res = yield call(mrfInterviewService.getInterviewDetailsById, dataParams);

    if (res.data.error) {
      yield put(actions.getInterviewDetailsByIdError(res));

    } else {
      yield put(actions.getInterviewDetailsByIdSuccess(res));
    }

  } catch (error) {
    yield put(actions.getInterviewDetailsByIdError());
  }
}


// -----------Watchers-------------

function* handleGetAssignedInterviewWatcher() {
  yield takeEvery(
    actions.getAssignedInterviewRequest,
    handleGetAssignedInterviewRequest,
  );
}
function* handleGetQuestionByInterviewStageWatcher() {
  yield takeEvery(
    actions.getQuestionByInterviewStageRequest,
    handleGetQuestionByInterviewStageRequest,
  );
}

function* handleFillFeedbackFormWatcher() {
  yield takeEvery(
    actions.fillFeedbackFormRequest,
    handleFillFeedbackFormRequest,
  );
}

function* handleGetInterviewDetailsByIdWatcher() {
  yield takeEvery(
    actions.getInterviewDetailsByIdRequest,
    handleGetInterviewDetailsByIdRequest,
  );
}

function* rootMrfInterviewSaga() {
  yield all([

    handleGetAssignedInterviewRequest(),
    handleGetAssignedInterviewWatcher(),

    handleGetQuestionByInterviewStageRequest(),
    handleGetQuestionByInterviewStageWatcher(),

    handleFillFeedbackFormRequest(),
    handleFillFeedbackFormWatcher(),

    handleGetInterviewDetailsByIdRequest(),
    handleGetInterviewDetailsByIdWatcher(),
  ]);
}

export default rootMrfInterviewSaga;
